'use strict';

define('mine',['React', 'navBar', 'util'], function (React, NavBar, Util) {
    'use strict';
    //defaultChecked="checked"
    var lock = false,
        languageLock = false;

    var Mine = React.createClass({
        displayName: 'Mine',

        render: function render() {
            var _isOpen = this.props.open ? 'open' : '';
            var apps = this.state.apps,
                seting = '',
                praise = '',
                language = this.props.language.mine,
                pushEnabledStr = this.state.pushEnabled ? language.push_ios.status[0] : language.push_ios.status[1];

            // ios version > 1.1    android version > 1.2
            if (apps.isApp && this.props.open) {
                if (apps.type == 'iOS') {
                    seting = React.createElement(
                        'li',
                        null,
                        React.createElement(
                            'span',
                            null,
                            language.push_ios.title
                        ),
                        React.createElement(
                            'span',
                            { className: 'pushenabled' },
                            pushEnabledStr
                        ),
                        React.createElement(
                            'p',
                            null,
                            language.push_ios.info
                        )
                    );

                    praise = React.createElement(
                        'li',
                        { className: 'arr-right', onClick: this.clickPraise },
                        ' ',
                        language.comment
                    );
                } else if (apps.type == 'Android') {
                    var _checked = this.state.pushEnabled ? 'checked' : '';
                    seting = React.createElement(
                        'li',
                        null,
                        React.createElement(
                            'span',
                            null,
                            language.push_android.title
                        ),
                        React.createElement('input', { className: 'mui-switch mui-switch-anim', type: 'checkbox', defaultChecked: _checked, onChange: this.pushNotification }),
                        React.createElement(
                            'p',
                            null,
                            language.push_android.info
                        )
                    );
                }
            }
            /**/
            return React.createElement(
                'div',
                { className: 'movie-detail mine-page ' + _isOpen },
                React.createElement(NavBar, {
                    rightLogo: true,
                    leftNav: ' ',
                    title: language.titlebar,
                    backCallback: this.props.backCallback }),
                React.createElement(
                    'ul',
                    { className: 'myset' },
                    seting,
                    React.createElement(
                        'li',
                        null,
                        React.createElement(
                            'span',
                            null,
                            language.language
                        ),
                        React.createElement('input', { className: 'mui-switch mui-switch-anim', type: 'checkbox', onChange: this.changeLanguage })
                    ),
                    React.createElement(
                        'li',
                        { className: 'arr-right', onClick: this.clickGotoFeedblock },
                        ' ',
                        language.feedback
                    ),
                    praise,
                    React.createElement(
                        'li',
                        null,
                        ' ',
                        language.version,
                        ' ',
                        React.createElement(
                            'em',
                            null,
                            apps.version
                        )
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {
                apps: Util.compareVersion(),
                pushEnabled: null
            };
        },
        componentDidMount: function componentDidMount() {},
        pushNotification: function pushNotification(evt) {
            // console.log(evt)
            var checked = evt.target.checked;
            if (checked) {
                if (window.XGPush && window.XGPush.setOn) {
                    window.XGPush.setOn('');
                }
            } else {
                if (window.XGPush && window.XGPush.setOff) {
                    window.XGPush.setOff('');
                }
            }
        },
        changeLanguage: function changeLanguage(evt) {
            var checked = evt.target.checked,
                languageType = this.props.language.type == 'cn' ? 'en' : 'cn';
            if (checked && !languageLock) {
                languageLock = true;
                localStorage.setItem('language', languageType);
                // location.reload();
                Util.toast(languageType == 'cn' ? '...' : 'Setting...', 2e3);
                setTimeout(function () {
                    location.href = '/';
                }, 1800);
            }
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var apps = this.state.apps;
            // ios version > 1.1    android version > 1.2
            if (apps.isApp && !lock) {
                lock = true;
                this.appinit();
            }

            var _isOpen = nextProps.open;
            var _el = document.getElementsByClassName('mine-page')[0];
            if (!_isOpen) {
                _el.className = 'movie-detail mine-page';
                return false;
            } else {
                _el.className = 'movie-detail mine-page open';
            }
            return nextProps.open;
        },
        appinit: function appinit() {
            var self = this;
            // var oHead = document.getElementsByTagName('HEAD').item(0);
            // var oScript= document.createElement("script");
            // oScript.type = "text/javascript";
            // oScript.src="js/html2canvas.min.js";
            // oHead.appendChild( oScript);
            if (this.state.apps.type == 'iOS' && !window.isiOSInit) {
                window.isiOSInit = true;
                this.connectWebViewJavascriptBridge(function (bridge) {
                    bridge.init(function (message, responseCallback) {
                        if (responseCallback) {
                            responseCallback();
                        }
                    });
                });
            }

            if (this.state.apps.type == 'Android') {
                //
                window.androidXgpushCallBack = function (response) {
                    // alert('Android0:' + typeof response)

                    // console.log('Android');
                    self.setState({
                        pushEnabled: response
                    });
                    self.setAndroidXgpush(response);
                };
                //
                // alert('window.XGPush0:' + window.XGPush)
                // alert('window.XGPush1:' + window.XGPush.getStatus)
                // alert('window.setOn:' + window.XGPush.setOn)
                // alert('window.setOff:' + window.XGPush.setOff)
                if (window.XGPush && window.XGPush.getStatus) {
                    window.XGPush.getStatus('androidXgpushCallBack');
                }
            } else if (this.state.apps.type == 'iOS') {
                this.connectWebViewJavascriptBridge(function (bridge) {
                    bridge.callHandler('checkPushStatus', '', function (response) {
                        console.log('iOS');
                        // alert(response.pushEnabled)
                        if (response) {
                            self.setState({
                                pushEnabled: response.pushEnabled
                            });
                        }
                    });
                });
            }
        },

        //iOS 
        connectWebViewJavascriptBridge: function connectWebViewJavascriptBridge(callback) {
            if (window.WebViewJavascriptBridge) {
                callback(WebViewJavascriptBridge);
            } else {
                document.addEventListener('WebViewJavascriptBridgeReady', function () {
                    callback(WebViewJavascriptBridge);
                }, false);
            }
        },
        clickGotoFeedblock: function clickGotoFeedblock() {
            location.hash = '/feedback';
        },
        clickPraise: function clickPraise() {
            window.location.href = 'https://itunes.apple.com/cn/app/wei-piao-er-piao-fang-fen-xi/id1048372216?mt=8';
            // alert(window.location.href)
            //https://itunes.apple.com/cn/app/wei-piao-er-piao-fang-fen-xi/id1048372216?mt=8
            //https://appsto.re/cn/4poF-.i
        },

        setAndroidXgpush: function setAndroidXgpush(response) {
            this.setState({
                pushEnabled: response
            });
            console.log(this.state.pushEnabled);
        },

        setPiaoFang: function setPiaoFang() {
            ga('send', 'pageview', {
                'page': '/mine',
                'title': ''
            });
            //Hawkeye - Wepiao FE Analytics Solution
            he && he('send', 'pageview', 'mine');
        }

    });

    return Mine;
});
