'use strict';

define('SwitchToDate',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'util', '../mock/city', 'SelectDate'], function (React, Chart, ReactChart, IScroll, Router, Util, CITY, SelectDate) {
    'use strict';
    // <input type="date" value="2011-01-04">

    var SwitchToDate = React.createClass({
        displayName: 'SwitchToDate',

        days: null,
        render: function render() {
            var _handleClick = this.handlebarClick,
                language = window._language ? window._language.util.switchToDate : { th1: '', th2: '' },
                next_time = document.getElementsByClassName('_next_time_'),
                style = 'nav_time _next_time_';
            if (this.state.days === this.props.maxDays) {
                style = 'nav_time _next_time_ color-h';
            }
            var _maxDate = Util.getIndexDaysStr(Util.getTodayStr(), this.props.maxDays);
            return React.createElement(
                'div',
                { className: 'nav_list border-1px' },
                React.createElement(
                    'div',
                    { className: 'nav_time', onClick: _handleClick.bind(null, 'prev') },
                    React.createElement(
                        'span',
                        { className: 'arr-left' },
                        language.th1
                    )
                ),
                React.createElement(SelectDate, { ref: 'SelectDate', selectDateCallback: this.selectDateCallback, date: this.state.date, MAXDate: _maxDate, isAds: this.state.isAds }),
                React.createElement(
                    'div',
                    { className: style, onClick: _handleClick.bind(null, 'next') },
                    React.createElement(
                        'span',
                        { className: 'arr-right' },
                        language.th2
                    )
                )
            );
        },
        getInitialState: function getInitialState() {
            return {
                date: this.props.date || Util.getTodayStr(),
                days: this.props.days,
                isAds: false
            };
        },

        componentDidMount: function componentDidMount() {},
        componentDidUpdate: function componentDidUpdate() {},
        cityLisDialog: function cityLisDialog() {},
        handlebarClick: function handlebarClick(eventName) {
            var _days = this.state.days;
            var _maxDays = this.props.maxDays || 2;
            if (_days === _maxDays && eventName == 'next') {
                return;
            }
            var next_time = document.getElementsByClassName('_next_time_');
            if (next_time[0]) {
                next_time[0].className = 'nav_time _next_time_';
            }
            var _date;

            if (eventName == 'prev') {
                _date = Util.prevDay(this.state.date);
                _days--;
                Util.setPiaoFangEventGa('', 'prev', _date, _date);
                // ga('send', 'event', 'button', 'click', '' , 1);
                // //Hawkeye - Wepiao FE Analytics Solution
                // he && he('send', 'event', 'button', 'click', 'before', 1);
            } else {
                _days++;
                if (_days === _maxDays) {
                    if (next_time[0]) {
                        next_time[0].className = 'nav_time _next_time_ color-h';
                    }
                    // _date = Util.nextDay(this.state.date);
                } else if (_days > _maxDays) {
                    if (next_time[0]) {
                        next_time[0].className = 'nav_time _next_time_ color-h';
                    }
                    return;
                }
                _date = Util.nextDay(this.state.date);
                Util.setPiaoFangEventGa('', 'next', _date, _date);
                // ga('send', 'event', 'button', 'click', '' , 1);
                // //Hawkeye - Wepiao FE Analytics Solution
                // he && he('send', 'event', 'button', 'click', 'after', 1);
            }
            this.setState({ date: _date, days: _days });
            this.refs.SelectDate.setState({ date: _date });

            this.props.selectDateChange(_date);
        },

        selectDateCallback: function selectDateCallback(_date) {
            var _nowDate = this.state.date.replace(' 00:00:00', '');
            var _selectDate = _date.replace(' 00:00:00', '');
            var _days = Util.restrictSwitchToDate(_nowDate, _selectDate);
            this.setState({
                date: _date,
                days: _days
            });
            this.props.selectDateChange(_date);
        }
    });

    return SwitchToDate;
});

