'use strict';

define('yearRank',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'navBar', 'util', 'datacenter', 'commonTable', 'SelectDate', 'feedbackBlock', 'share'], function (React, Chart, ReactChart, IScroll, Router, NavBar, Util, DataCenter, CommonTable, SelectDate, FeedbackBlock, Share) {
    'use strict';

    //
    var currentDay = Util.prevDay(Util.getTodayStr());

    // 5
    var loseIndex = 1;
    var locality = 'ALL';

    //
    var params = {
        'paging': {
            'page': 1,
            'pageSize': 100
        },
        'movieFilter': {
            'year': 0,
            'locality': locality
        }
    };

    var style = {
        background: 0,
        border: 0,
        fontSize: '1.22rem',
        color: '#D4D2D2'
    };

    // Component
    var YearRank = React.createClass({
        displayName: 'YearRank',

        render: function render() {
            var _current = this.state.currentYeay,
                _onClick = this.onTabClick,
                _onClickLocality = this.onTabLocality,
                _currentLocality = this.state.locality,
                _onClickFilterBtn = this.onClickFilterBtn,
                language = this.props.language;

            return React.createElement(
                'section',
                { className: '_year_rank ' + (this.props.active ? 'active' : 'cached') },
                React.createElement(
                    'div',
                    { className: 'raw-scroller ', style: { 'display': this.state.raw_scroller_status ? 'block' : 'none' } },
                    React.createElement(
                        'ul',
                        { className: 'css-1px year-rank', style: { 'width': '460px' } },
                        React.createElement(
                            'li',
                            { className: _current == 0 ? 'current' : '',
                                onClick: _onClick.bind(null, 0) },
                            React.createElement(
                                'a',
                                null,
                                language.screen[0]
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2016 ? 'current' : '',
                                onClick: _onClick.bind(null, 2016) },
                            React.createElement(
                                'a',
                                null,
                                '2016'
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2015 ? 'current' : '',
                                onClick: _onClick.bind(null, 2015) },
                            React.createElement(
                                'a',
                                null,
                                '2015'
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2014 ? 'current' : '',
                                onClick: _onClick.bind(null, 2014) },
                            React.createElement(
                                'a',
                                null,
                                '2014'
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2013 ? 'current' : '',
                                onClick: _onClick.bind(null, 2013) },
                            React.createElement(
                                'a',
                                null,
                                '2013'
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2012 ? 'current' : '',
                                onClick: _onClick.bind(null, 2012) },
                            React.createElement(
                                'a',
                                null,
                                '2012'
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _current == 2011 ? 'current' : '',
                                onClick: _onClick.bind(null, 2011) },
                            React.createElement(
                                'a',
                                null,
                                '2011'
                            )
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'raw-scroller raw-scroller-2 ', style: { 'display': this.state.raw_scroller_status ? 'block' : 'none' } },
                    React.createElement(
                        'ul',
                        { className: 'css-1px country-rank', style: { 'width': '460px' } },
                        React.createElement(
                            'li',
                            { className: _currentLocality == 'ALL' ? 'current' : '',
                                onClick: _onClickLocality.bind(null, 'ALL') },
                            React.createElement(
                                'a',
                                null,
                                language.screen[1][0]
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _currentLocality == 'DOMESTIC' ? 'current' : '',
                                onClick: _onClickLocality.bind(null, 'DOMESTIC') },
                            React.createElement(
                                'a',
                                null,
                                language.screen[1][1]
                            )
                        ),
                        React.createElement(
                            'li',
                            { className: _currentLocality == 'IMPORTED' ? 'current' : '',
                                onClick: _onClickLocality.bind(null, 'IMPORTED') },
                            React.createElement(
                                'a',
                                null,
                                language.screen[1][2]
                            )
                        )
                    )
                ),
                React.createElement(
                    'h2',
                    { className: 'rank-title css-1px' },
                    _current == 0 ? language.title1 : language.title2 + this.state.model.nationalBoxOffice + '',
                    React.createElement('i', { onClick: _onClickFilterBtn.bind(), className: 'icon-loudou ' + (this.state.icon_loudou ? '' : 'current') })
                ),
                React.createElement(
                    'div',
                    { className: 'contentView withFooter withSegmented withPickerRank ra-scroller', style: { 'top': this.state.raw_scroller_status ? '' : '129px' } },
                    React.createElement(
                        'ul',
                        { className: 'scroller' },
                        React.createElement(CommonTable, {
                            th1: language.item.th1,
                            th2: language.item.th2,
                            temp_th: language.item.th3,
                            th3: language.item.th4,
                            tr1_field: language.item.movieName,
                            tr2_field: language.item.productTotalBoxOffice,
                            temp_field: language.item.productTickets,
                            tr3_field: 'productAvgPerson',
                            id_field: 'cinemaId',
                            name_field: 'rankName',
                            index: true,
                            movieList: this.state.model && this.state.model.movieBoxOffices
                        }),
                        React.createElement(FeedbackBlock, null)
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {
                model: null,
                date: currentDay,
                currentYeay: 0,
                locality: 'ALL',
                raw_scroller_status: true,
                icon_loudou: true
            };
        },
        componentDidMount: function componentDidMount() {
            this.fetchData();

            //this.initIScroll();
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var _active = nextProps.active;
            var _el = document.getElementsByClassName('_year_rank')[0];
            if (!_active) {
                _el.className = '_year_rank cached';
                return false;
            } else {
                _el.className = '_year_rank active';
            }
            return true;
        },
        componentDidUpdate: function componentDidUpdate() {
            if (this.props.active) {

                this.CIScroll && this.CIScroll.refresh();
                this.RAWIScroll && this.RAWIScroll.refresh();
                if (!this.props.detail_open) {
                    Share.setShare('');
                }
                // ga('send', 'pageview', {
                //     'page': '/rank',
                //     'title': ''
                // });

                // //Hawkeye - Wepiao FE Analytics Solution
                // he && he('send', 'pageview', 'OverallRecord');

                // app
                window.shareinfo = {
                    title: '',
                    desc: '',
                    url: 'http://piaofang.wepiao.com/#/rank',
                    height: 770
                };
                window.app_iScrolls[7] = this.CIScroll;
            }
        },
        fetchData: function fetchData(_params) {
            var self = this;
            if (!_params) {
                _params = params;
            }

            DataCenter.YRankModel.getData(_params, function (res) {
                console.log('CIModel is ready:');
                console.log(res);
                // if(res.movieBoxOfficeRanks.length === 0){
                //     self.loseMethod();
                // }
                // re-render
                self.setState({
                    model: res
                });
                setTimeout(self.initIScroll, 500);
            }, function (err) {
                console.log(err);
            });
        },
        initIScroll: function initIScroll() {
            //init IScroll
            this.CIScroll = new IScroll('.ra-scroller', {
                mouseWheel: true,
                bindToWrapper: true,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
            if (!this.RAWIScroll) {
                this.RAWIScroll = new IScroll('.raw-scroller', {
                    scrollX: true,
                    scrollY: false,
                    mouseWheel: true,
                    bindToWrapper: true,
                    preventDefault: Util.iScrollClick(),
                    tap: Util.iScrollClick(),
                    click: Util.iScrollClick()
                });
            }
        },

        //
        selectDateCallback: function selectDateCallback(_date) {
            console.log(_date);
            //alert(_date);
            params.movieFilter.scheduleDate = _date + ' 00:00:00';
            this.fetchData(params);
        },

        onTabClick: function onTabClick(_currentYeay) {
            var _currentYeaystr = _currentYeay;
            if (_currentYeay == 0) {
                _currentYeaystr = '';
            }
            this.setState({
                currentYeay: _currentYeay
            });
            params.movieFilter.year = _currentYeay;
            this.fetchData();

            Util.setPiaoFangEventGa('', 'OverallRecord', 'year_' + _currentYeaystr, 'year_' + _currentYeaystr);
            // ga('send', 'event', 'YearRank', 'click', '_'+ _currentYeaystr , 1);

            // //Hawkeye - Wepiao FE Analytics Solution
            // he && he('send', 'event', 'YearRank', 'click', 'year_' + _currentYeaystr, 1);
        },

        onTabLocality: function onTabLocality(_locality) {
            var _localitystr = '';
            this.setState({
                locality: _locality
            });
            params.movieFilter.locality = _locality;
            if (_locality == 'ALL') {
                params.paging.pageSize = 100;
            } else {
                params.paging.pageSize = 30;
                if (_locality == 'DOMESTIC') {
                    _localitystr = '';
                } else {
                    _localitystr = '';
                }
            }
            this.fetchData();

            Util.setPiaoFangEventGa('', 'overallrecord', _localitystr, _locality);

            // ga('send', 'event', 'YearRank', 'click', '_'+ _localitystr , 1);

            // //Hawkeye - Wepiao FE Analytics Solution
            // he && he('send', 'event', 'YearRank', 'click', 'movietype_' + _locality, 1);
        },

        // 
        onClickFilterBtn: function onClickFilterBtn(evt) {
            var _status = this.state.raw_scroller_status;
            if (_status) {
                this.setState({
                    raw_scroller_status: false,
                    icon_loudou: false
                });
            } else {
                this.setState({
                    raw_scroller_status: true,
                    icon_loudou: true
                });
            }
            this.CIScroll && this.CIScroll.refresh();
        }
    });

    return YearRank;
});
