'use strict';

define('rank',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'navBar', 'util', 'datacenter', 'commonTable', 'SelectDate', 'feedbackBlock', 'share', 'segmented', 'yearRank', 'dayRank', 'wxRank'], function (React, Chart, ReactChart, IScroll, Router, NavBar, Util, DataCenter, CommonTable, SelectDate, FeedbackBlock, Share, Segmented, YearRank, DayRank, WxRank) {
    'use strict';

    //
    var currentDay = Util.prevDay(Util.getTodayStr());

    // 5
    var loseIndex = 1;

    //
    var params = {
        'paging': {
            'page': 1,
            'pageSize': 100
        },
        'movieFilter': {
            'year': 0
        }
    };

    var style = {
        background: 0,
        border: 0,
        fontSize: '1.22rem',
        color: '#D4D2D2'
    };

    // Component
    var Rank = React.createClass({
        displayName: 'Rank',

        render: function render() {
            var _current = this.state.currentSegment,
                language = this.props.language.rank;

            return React.createElement(
                'div',
                { className: 'movie-rank ' + this.props.active
                },
                React.createElement(Segmented, {
                    current: this.state.currentSegment,
                    th1: language.menutitle[0],
                    th2: language.menutitle[1],
                    th3: language.menutitle[2],
                    onChange: this.onSegmentedChange
                }),
                React.createElement(NavBar, {
                    bigLogo: true, title: 'Lynn\'s Box',
                    clickSearch: this.props.callbackSearch
                }),
                React.createElement(WxRank, {
                    active: _current == 0 && this.props.active == 'active',
                    detail_open: this.state.detail_open,
                    language: language.wx,
                    onItemClick: this.props.onMovieItemClick
                }),
                React.createElement(YearRank, {
                    active: _current == 1,
                    detail_open: this.state.detail_open,
                    language: language.yearRank
                }),
                React.createElement(DayRank, {
                    active: _current == 2,
                    detail_open: this.state.detail_open,
                    language: language.dayRank
                })
            );
        },
        getInitialState: function getInitialState() {
            return {
                model: null,
                date: currentDay,
                currentSegment: 0,
                detail_open: this.props.detail_open
            };
        },
        componentDidMount: function componentDidMount() {},
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var _active = nextProps.active;
            var _el = document.getElementsByClassName('movie-rank')[0];
            if (_active != 'active') {
                _el.className = 'movie-rank cached';
                return false;
            } else {
                _el.className = 'movie-rank active';
            }
            return true;
        },
        componentDidUpdate: function componentDidUpdate() {
            if (this.props.active == 'active') {}
        },
        fetchData: function fetchData(_params) {
            var self = this;
            if (!_params) {
                _params = params;
            }

            DataCenter.YRankModel.getData(_params, function (res) {
                console.log('CIModel is ready:');
                console.log(res);
                // if(res.movieBoxOfficeRanks.length === 0){
                //     self.loseMethod();
                // }
                // re-render
                self.setState({
                    model: res
                });
            }, function (err) {
                console.log(err);
            });
        },
        initIScroll: function initIScroll() {
            //init IScroll
            this.CIScroll = new IScroll('.ra-scroller', {
                mouseWheel: true,
                bindToWrapper: true,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
        },

        //
        selectDateCallback: function selectDateCallback(_date) {
            console.log(_date);
            //alert(_date);
            params.movieFilter.scheduleDate = _date + ' 00:00:00';
            this.fetchData(params);
        },

        // Segmented Control
        onSegmentedChange: function onSegmentedChange(index) {
            var pullRefreshTop;
            if (index == 0) {
                pullRefreshTop = 120;
                Util.setPiaoFangEventGa('', 'Rank', '', 'WechatIndex');
            } else if (index >= 1) {
                pullRefreshTop = 10;
                if (index == 1) {
                    Util.setPiaoFangEventGa('', 'Rank', '', 'OverallRecord');
                } else if (index == 2) {
                    Util.setPiaoFangEventGa('', 'Rank', '', 'DailyRecord');
                }
            }
            this.props.pullRefreshCallBack(pullRefreshTop);
            this.setState({
                currentSegment: index,
                isOnClickMovieDetailMenus: true
            });
        },
        setPiaoFang: function setPiaoFang() {
            ga('send', 'pageview', {
                'page': '/rank',
                'title': ''
            });
            //Hawkeye - Wepiao FE Analytics Solution
            he && he('send', 'pageview', 'rank');
        }
    });

    return Rank;
});

