/**
 *  Feedback Page.jsx
 *  
 *
 *  @props  {Bool}      open            
 *  @props  {Function}  backCallback    
 *  @return {Component} FeedbackPage
 */

'use strict';

define('adPage',['React', 'navBar', 'util', 'IScroll'], function (React, NavBar, Util, IScroll) {
    'use strict';

    // Component
    var AdPage = React.createClass({
        displayName: 'AdPage',

        render: function render() {
            var _isOpen = this.props.open ? 'open' : '',
                ad = window.adLists ? window.adLists[this.props.targeturlPageIndex] : null,
                _title = ad ? ad.title : '',
                targetUrl = '',
                _iframe = '';
            // targetUrl = (ad && this.props.open) ? decodeURIComponent(ad.url) : '';

            if (ad && this.props.open) {
                targetUrl = decodeURIComponent(ad.url);
                _iframe = React.createElement(
                    'div',
                    { className: 'targeturl-scroller', style: { '-webkit-overflow-scrolling': 'touch', 'overflow': 'auto', 'z-index': '100', 'height': '100%' } },
                    React.createElement('iframe', { id: 'iframepage', style: { 'position': 'relative', 'top': '40px', 'width': document.body.clientWidth, 'height': '100%', 'overflow': 'hidden' }, src: targetUrl })
                );
            }

            return React.createElement(
                'div',
                { className: 'movie-detail targeturl-page ' + _isOpen },
                React.createElement(NavBar, {
                    rightLogo: true,
                    leftNav: ' ',
                    title: _title,
                    backCallback: this.backCallback }),
                _iframe
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {};
        },
        getDefaultProps: function getDefaultProps() {
            return {};
        },
        componentDidMount: function componentDidMount() {
            // this.initIScroll();
            var ifm = document.getElementById('iframepage');
            if (ifm) {
                var _system = Util.compareSystem();
                if (_system === 'iOS') {
                    ifm.scrolling = 'no';
                }

                ifm.frameBorder = '0';
            }
        },
        componentDidUpdate: function componentDidUpdate() {
            if (this.props.open) {}
        },
        // shouldComponentUpdate: function(nextProps, nextState) {
        //     let open = nextProps.open;
        //     let _el = document.getElementsByClassName('targeturl-page')[0];
        //     if(open){
        //         _el.className = "movie-detail targeturl-page open";
        //         return true;
        //     }else{

        //         _el.className = "movie-detail targeturl-page ";
        //         return false;
        //     }

        // },
        initIScroll: function initIScroll() {
            var self = this;
            //init IScroll
            this.targeturlScroll = new IScroll('.targeturl-scroller', {
                scrollX: false,
                scrollY: true,
                mouseWheel: true,
                bindToWrapper: true,
                probeType: 3,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
        },
        backCallback: function backCallback() {
            this.props.backCallback('adpage');
        }
    });

    return AdPage;
});

