define('ReactChart/lib/core',['require', 'React', 'Chart'], function (require) {


var dataKeys = {
    'Line': 'points',
    'Radar': 'points',
    'Bar': 'bars'
};

var updatePoints = function(nextProps, chart, dataKey) {
    var name = chart.name;

    if (name === 'PolarArea' || name === 'Pie' || name === 'Doughnut') {
        // if data length different, error occured;
        // hacking commit :
        // code.wepiao.com/f2e/blackarrow/commit/045c02568b929d6020adc38ec13039e311f0b6d1#diff-7
        nextProps.data.forEach(function(segment, segmentIndex) {
            chart.segments[segmentIndex].value = segment.value;
            chart.segments[segmentIndex].label = segment.label;
        });
    } else {
        // update datasets, value
        nextProps.data.datasets.forEach(function(set, setIndex) {
            set.data.forEach(function(val, pointIndex) {
                chart.datasets[setIndex][dataKey][pointIndex].value = val;
                //chart.datasets[setIndex][dataKey][pointIndex].label = ;
            });
            // update labels!
            nextProps.data.labels.forEach(function(label, pointIndex) {
                chart.datasets[setIndex][dataKey][pointIndex].label = label;
            });
        });

    }
};

return {
    createClass: function(chartType, methodNames, dataKey) {
        var classData = {
            displayName: chartType + 'Chart',
            getInitialState: function() { return {}; },
            render: function() {
                var _props = {
                    ref: 'canvass'
                };
                for (var name in this.props) {
                    if (this.props.hasOwnProperty(name)) {
                        if (name !== 'data' && name !== 'options') {
                            _props[name] = this.props[name];
                        }
                    }
                }
                return React.createElement('canvas', _props);
            }
        };

        var extras = ['clear', 'stop', 'resize', 'toBase64Image', 'generateLegend', 'update', 'addData', 'removeData'];
        function extra(type) {
            classData[type] = function() {
                this.state.chart[name].apply(this.state.chart, arguments);
            };
        }

        classData.componentDidMount = function() {
            this.initializeChart(this.props);
        };

        classData.componentWillUnmount = function() {
            var chart = this.state.chart;
            chart.destroy();
        };

        classData.componentWillReceiveProps = function(nextProps) {
            // hack by Hux line 65-79
            // add blockRedraw to block useless redraw
            var chart = this.state.chart;
            var name = chart.name;
            var debug = false;

            // if length of chart data equal to nextProps data, block redraw!
            if (name === 'PolarArea' || name === 'Pie' || name === 'Doughnut') {
                var blockRedraw = nextProps.data.length === chart.segments.length;
            }else if(name === "Bar"){
                var blockRedraw = nextProps.data.labels.length === chart.datasets[0].bars.length;
            }else if(name === "Line"){
                var blockRedraw = nextProps.data.labels.length === chart.datasets[0].points.length;
            }

            // if need redraw and set props, do redraw. otherwise just update
            if (!blockRedraw && this.props.redraw) {
                if(debug) console.log('redraw');
                chart.destroy();
                this.initializeChart(nextProps);
            } else {
                if(debug) console.log('update');
                dataKey = dataKey || dataKeys[chart.name];
                updatePoints(nextProps, chart, dataKey);
                chart.update();
            }
        };

        classData.initializeChart = function(nextProps) {
            var Chart = require('Chart');
            var el = this.getDOMNode();
            var ctx = el.getContext("2d");
            var chart = new Chart(ctx)[chartType](nextProps.data, nextProps.options || {});
            this.state.chart = chart;
        };

        // return the chartjs instance
        classData.getChart = function() {
            return this.state.chart;
        };

        // return the canvass element that contains the chart
        classData.getCanvass = function() {
            return this.refs.canvass.getDOMNode();
        };

        var i;
        for (i=0; i<extras.length; i++) {
            extra(extras[i]);
        }
        for (i=0; i<methodNames.length; i++) {
            extra(methodNames[i]);
        }

        var React = require('React');
        return React.createClass(classData);
    }
};







});

