/**
 *  Movie Detail.jsx
 *     /
 *
 *  @props  {Bool}      open            
 *  @props  {Number}    movieId          ID
 *  @props  {String}    movieName       
 *  @props  {Number}    currentSegment  
 *  @props  {Function}  backCallback    
 *  @return {Component} MovieDetail
 */

'use strict';

define('movieDetail',['React', 'navBar', 'segmented', 'movieSchedule', 'movieBoxOffice', 'MovieData', 'util', 'datacenter', 'IScroll'], function (React, NavBar, Segmented, MovieSchedule, MovieBoxOffice, MovieData, Util, DataCenter, IScroll) {
    'use strict';
    window.React = React;
    // Component
    var MovieDetail = React.createClass({
        displayName: 'MovieDetail',

        render: function render() {
            var _isOpen = this.props.open ? 'open' : '',
                _title = this.props.movieName,
                _current = this.state.currentSegment,
                movie = '',
                language = this.props.language.movieDetail,
                isCN = this.props.language.type == 'cn' ? true : false,
                _movieData = '',
                _segmented = React.createElement(Segmented, {
                current: _current,
                th1: language.menutitle[0],
                th2: language.menutitle[1],
                onChange: this.onSegmentedChange });

            //
            if (this.state.movie) {
                var _movie = this.state.movie,
                    scoreEl = '',
                    movieHotEl = '',

                // starrings = _movie[language.movie.starring].split('/'),
                // starring = _movie[language.movie.director] +
                //            (starrings[0] ? ' / ' + starrings[0] : '') +
                //            (starrings[1] ? ' / ' + starrings[1] : ''),

                releaseDate = Util.getDateStr(new Date(_movie.releaseDate));
                var _days = Util.restrictSwitchToDate(Util.getTodayStr(), releaseDate);
                if (_days > 0) {
                    if (_movie.wantCount > 0) {
                        scoreEl = React.createElement(
                            'div',
                            null,
                            React.createElement('i', { className: 'ico-popcorn' }),
                            Util.getFormattedNum(_movie.wantCount),
                            React.createElement(
                                'small',
                                null,
                                language.movie.reviewitem[2]
                            )
                        );
                    }
                } else {
                    if (_movie.score > 0) {
                        scoreEl = React.createElement(
                            'div',
                            null,
                            React.createElement('i', { className: 'ico-popcorn' }),
                            _movie.score,
                            '%',
                            React.createElement(
                                'small',
                                null,
                                language.movie.reviewitem[0]
                            )
                        );
                    }
                }
                if (_movie.movieHot > 0) {
                    movieHotEl = React.createElement(
                        'div',
                        null,
                        React.createElement('i', { className: 'ico-wechat' }),
                        Util.getFormattedNum(_movie.movieHot),
                        React.createElement(
                            'small',
                            null,
                            language.movie.reviewitem[1]
                        )
                    );
                }
                //
                movie = React.createElement(
                    'div',
                    { className: 'movie-detail-wrapper' },
                    React.createElement(
                        'div',
                        { className: 'movie-details' },
                        React.createElement('img', { src: _movie.pictureUrl }),
                        React.createElement(
                            'div',
                            { className: 'mov-info' },
                            React.createElement(
                                'div',
                                { className: 'mov-data' },
                                scoreEl,
                                movieHotEl
                            ),
                            React.createElement(
                                'p',
                                null,
                                _movie[language.movie.country]
                            ),
                            React.createElement(
                                'p',
                                null,
                                _movie[language.movie.productReleaseDate],
                                ', ',
                                _movie[language.movie.duration]
                            ),
                            React.createElement(
                                'p',
                                null,
                                _movie.version
                            )
                        )
                    )
                );

                if (isCN) {
                    _movieData = React.createElement(MovieData, {
                        active: _current == 2,
                        open: this.props.open,
                        movie: this.state.movie,
                        movieScrollRefresh: this.movieScrollRefresh,
                        movieId: this.props.movieId
                    });

                    _segmented = React.createElement(Segmented, {
                        current: _current,
                        th1: language.menutitle[0],
                        th2: language.menutitle[1],
                        th3: language.menutitle[2],
                        onChange: this.onSegmentedChange });
                }
            }
            /**/
            return React.createElement(
                'div',
                { className: 'movie-detail _movie ' + _isOpen },
                React.createElement(NavBar, {
                    title: this.props.movieName,
                    leftNav: ' ',
                    rightLogo: true,
                    backCallback: this.props.backCallback }),
                React.createElement(
                    'div',
                    { className: 'movie-detail-container' },
                    React.createElement(
                        'div',
                        { className: 'movie-scroller' },
                        movie,
                        _segmented,
                        React.createElement(MovieSchedule, {
                            active: _current == 1,
                            open: this.props.open,
                            movieId: this.props.movieId,
                            maxDays: this.props.maxDays,
                            movieName: _title,
                            movieCallBack: this.movieCallBack,
                            movieScrollRefresh: this.movieScrollRefresh,
                            showDailogCallback: this.showDailogCallback,
                            scrollBarToZeroCallback: this.scrollBarToZeroCallback,
                            language: language.schedule,
                            isCN: isCN }),
                        React.createElement(MovieBoxOffice, {
                            active: _current == 0,
                            open: this.props.open,
                            movieId: this.props.movieId,
                            movieName: _title,
                            movieCallBack: this.movieCallBack,
                            movieScrollRefresh: this.movieScrollRefresh,
                            language: language.boxoffice,
                            isCN: isCN }),
                        _movieData
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {
                movieId: 0,
                currentSegment: 1,
                movie: null,
                days: 0
            };
        },
        getDefaultProps: function getDefaultProps() {
            return {
                movieName: ''
            };
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var _isOpen = nextProps.open;
            var _el = document.getElementsByClassName('_movie')[0];
            if (!_isOpen) {
                _el.className = 'movie-detail _movie';
                return false;
            } else {
                _el.className = 'movie-detail _movie open';
            }
            return true;
            //return nextProps.open;
        },
        componentDidMount: function componentDidMount() {
            this.initIScroll();
        },
        componentWillReceiveProps: function componentWillReceiveProps(nextProps) {
            if (nextProps.currentSegment !== 'undefined') {
                var currentSegment = nextProps.currentSegment >= 2 ? 0 : nextProps.currentSegment;
                this.setState({
                    currentSegment: currentSegment
                });
            }
        },

        initIScroll: function initIScroll() {
            var _container = document.getElementsByClassName('movie-detail-container')[0];
            _container.style.height = document.body.clientHeight - 44 + 'px';

            this.movieScroll = new IScroll('.movie-detail-container', {
                mouseWheel: true,
                bindToWrapper: true,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
            this.movieScroll.on('scroll', this.doSomething);
        },

        movieScrollRefresh: function movieScrollRefresh() {
            this.movieScroll && this.movieScroll.refresh();
        },

        // Segmented Control
        onSegmentedChange: function onSegmentedChange(index) {
            this.setState({
                currentSegment: index,
                isOnClickMovieDetailMenus: true
            });
        },

        doSomething: function doSomething() {
            consols.log(this.x, this.y);
        },

        //
        movieCallBack: function movieCallBack(movie) {
            var releaseDate = Util.getDateStr(new Date(movie.releaseDate));
            var _days = Util.restrictSwitchToDate(Util.getTodayStr(), releaseDate);
            this.setState({
                movie: movie,
                days: _days
            });
        },

        //
        showDailogCallback: function showDailogCallback() {
            this.movieScroll && this.movieScroll.scrollTo(0, -150);
        },

        //0
        scrollBarToZeroCallback: function scrollBarToZeroCallback() {
            this.movieScroll && this.movieScroll.scrollTo(0, 0);
        }
    });

    return MovieDetail;
});
