'use strict';

define('comingMovieTable',['React', 'util', 'datacenter', 'IScroll', 'pullRefresh'], function (React, Util, DataCenter, IScroll, PullRefresh) {
    'use strict';
    // Component
    var ComingMovieTable = React.createClass({
        displayName: 'ComingMovieTable',

        getInitialState: function getInitialState() {
            return {
                model: [],
                language: null
            };
        },
        fetchData: function fetchData(_params) {
            var self = this;
            DataCenter.ComingMovieModel.getData(_params, function (res) {
                // re-render
                self.setState({
                    model: res.movies
                });
                self.initIScroll();
            }, function (err) {
                console.log(err);
            });
        },
        initIScroll: function initIScroll() {
            var self = this;
            //init IScroll
            this.COScroll = new IScroll('.co-scroller', {
                mouseWheel: true,
                bindToWrapper: true,
                probeType: 3,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
            var pullRefresh = new PullRefresh.refresh({
                scroll: this.COScroll,
                model: DataCenter.ComingMovieModel,
                params: self.props.params,
                callback: function callback(res) {
                    // re-render
                    self.setState({
                        model: res
                    });
                }
            });
            window.app_iScrolls[0] = this.COScroll;
        },
        componentDidMount: function componentDidMount() {
            var that = this,
                props = that.props,
                params = props.params,
                language = props.language;
            this.fetchData(params);
            this.setState({ language: language });
        },
        componentWillReceiveProps: function componentWillReceiveProps(nextProps) {
            var language = nextProps.language;
            this.fetchData(nextProps.params);
            this.setState({ language: language });
        },
        render: function render() {
            var that = this,
                state = this.state,
                movie = state.model,
                language = state.language;
            if (!language) {
                language = that.props.language;
            }
            var renderMovie = function renderMovie(item, i) {
                var moviePosterUrl = item.pictureUrl,
                    movieName = item[language.item_name],
                    movieInfor = item[language.item_basic],
                    movieReleaseDate = item[language.item_date],
                    movieWantCount = item[language.item_want],
                    movieItem = React.createElement(
                    'li',
                    { className: 'coming-item' },
                    React.createElement('img', { className: 'coming-img', src: moviePosterUrl }),
                    React.createElement(
                        'div',
                        { className: 'coming-info' },
                        React.createElement(
                            'h1',
                            null,
                            movieName
                        ),
                        React.createElement(
                            'p',
                            null,
                            movieInfor
                        ),
                        React.createElement(
                            'p',
                            null,
                            movieReleaseDate
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'coming-person' },
                        React.createElement(
                            'p',
                            null,
                            movieWantCount
                        )
                    )
                );
                return movieItem;
            };
            return React.createElement(
                'div',
                { className: 'co-scroller contentView coming' },
                React.createElement(
                    'ul',
                    { className: 'scroller' },
                    movie.map(renderMovie)
                )
            );
        }
    });

    return ComingMovieTable;
});
