'use strict';

define('MovieData',['React', 'navBar', 'segmented', 'movieSchedule', 'movieBoxOffice', 'util', 'datacenter', 'IScroll', 'share'], function (React, NavBar, Segmented, MovieSchedule, MovieBoxOffice, Util, DataCenter, IScroll, Share) {
    'use strict';
    window.React = React;
    // Component
    var MovieData = React.createClass({
        displayName: 'MovieData',

        render: function render() {
            var _movie = this.props.movie,
                _production = '',
                _distribution = '';

            if (_movie.production && _movie.production != '') {
                _production = React.createElement(
                    'li',
                    null,
                    React.createElement(
                        'p',
                        null,
                        ''
                    ),
                    React.createElement(
                        'p',
                        null,
                        _movie.production
                    )
                );
            }
            //
            if (_movie.distribution && _movie.distribution != '') {
                _distribution = React.createElement(
                    'li',
                    null,
                    React.createElement(
                        'p',
                        null,
                        ''
                    ),
                    React.createElement(
                        'p',
                        null,
                        _movie.distribution
                    )
                );
            }
            //
            return React.createElement(
                'section',
                { className: '_moviedata ' + (this.props.active ? 'active' : 'cached') },
                React.createElement(
                    'div',
                    { className: 'contentView withSegmented mdbo-scroller' },
                    React.createElement(
                        'ul',
                        { className: 'scroller movdetail-data' },
                        React.createElement(
                            'li',
                            null,
                            React.createElement(
                                'p',
                                null,
                                '',
                                _movie.director
                            ),
                            React.createElement(
                                'p',
                                null,
                                '',
                                _movie.starring
                            ),
                            React.createElement(
                                'p',
                                null,
                                ''
                            ),
                            React.createElement(
                                'p',
                                null,
                                _movie.detail
                            )
                        ),
                        _production,
                        _distribution
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {};
        },
        getDefaultProps: function getDefaultProps() {
            return {
                movieName: ''
            };
        },
        componentWillReceiveProps: function componentWillReceiveProps(nextProps) {
            var self = this;
            setTimeout(function () {
                self.props.movieScrollRefresh();
            }, 400);
        }

    });

    return MovieData;
});
