'use strict';

define('wxRank',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'navBar', 'util', 'datacenter', 'commonTable', 'SelectDate', 'feedbackBlock', 'share', 'pullRefresh'], function (React, Chart, ReactChart, IScroll, Router, NavBar, Util, DataCenter, CommonTable, SelectDate, FeedbackBlock, Share, PullRefresh) {
    'use strict';

    //
    var params = {
        'paging': {
            'page': 1,
            'pageSize': 10
        },
        'hotType': 'ONE_DAY'
    };
    window.IScroll = IScroll;
    var WxRank = React.createClass({
        displayName: 'WxRank',

        render: function render() {
            var model = this.state.model,
                movieHots = '',
                maxMovieHot = undefined,
                scale = undefined,
                _hotType = this.state.hotType,
                _onClickDays = this.onClickDays,
                language = this.props.language;
            if (model) {
                if (model.movieHots) {
                    movieHots = model.movieHots.map(function (movie, k) {
                        if (k == 0) {
                            maxMovieHot = movie.movieHot;
                        }
                        scale = Math.floor(movie.movieHot / maxMovieHot * 100);
                        var _isToday = Util.isEqualDays(movie.releaseDate, new Date()),
                            todayHtml = '',
                            tendencyCla = '',
                            _day = language.type == 'cn' ? Util.getChineseDateStr(movie.releaseDate) + language.item.release : Util.getEnglishDateStr(movie.releaseDate) + language.item.release;
                        if (_isToday) {
                            todayHtml = React.createElement(
                                'i',
                                null,
                                language.item.type
                            );
                        }
                        /**/
                        if (movie.tendency == -1) {
                            tendencyCla = 'icon-down';
                        } else if (movie.tendency == 0) {
                            tendencyCla = 'still';
                        } else if (movie.tendency == 1) {
                            tendencyCla = 'icon-up';
                        }

                        return React.createElement(
                            'li',
                            null,
                            React.createElement(
                                'span',
                                { className: 'start' },
                                React.createElement(
                                    'span',
                                    { className: 'cinema-index' },
                                    k + 1
                                ),
                                React.createElement(
                                    'h5',
                                    null,
                                    movie[language.item.movieName],
                                    todayHtml
                                ),
                                React.createElement(
                                    'p',
                                    null,
                                    _day
                                )
                            ),
                            React.createElement(
                                'span',
                                { className: 'middle' },
                                React.createElement(
                                    'div',
                                    null,
                                    React.createElement('h5', { className: 'transition5', style: { 'width': scale + '%' } })
                                ),
                                React.createElement(
                                    'p',
                                    null,
                                    movie.movieHot,
                                    React.createElement('i', { className: tendencyCla })
                                )
                            )
                        );
                    });
                }
            }
            return React.createElement(
                'section',
                { className: '_wx_rank ' + (this.props.active ? 'active' : 'cached') },
                React.createElement(
                    'ul',
                    { className: 'css-1px day-rank' },
                    React.createElement(
                        'li',
                        { className: _hotType == 'ONE_DAY' ? 'current' : '',
                            onClick: _onClickDays.bind(null, 'ONE_DAY') },
                        language.menutitle[0]
                    ),
                    React.createElement(
                        'li',
                        { className: _hotType == 'SEVEN_DAY' ? 'current' : '',
                            onClick: _onClickDays.bind(null, 'SEVEN_DAY') },
                        language.menutitle[1]
                    )
                ),
                '/**/',
                React.createElement(
                    'div',
                    { className: 'common-table table-rank wx-rank contentView withFooter withSegmented withPickerDaysRank' },
                    React.createElement(
                        'div',
                        { className: 'scroller' },
                        React.createElement(
                            'p',
                            { className: 'announcement css-1px' },
                            React.createElement('i', { className: 'icon-sound' }),
                            language.toast
                        ),
                        React.createElement(
                            'div',
                            { className: 'table-header css-1px' },
                            React.createElement(
                                'span',
                                { className: 'start' },
                                language.item.th1
                            ),
                            React.createElement(
                                'span',
                                { className: 'middle' },
                                language.item.th2
                            )
                        ),
                        React.createElement(
                            'ul',
                            { className: 'table-rank scroller' },
                            movieHots
                        ),
                        React.createElement(FeedbackBlock, null)
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {
                model: null,
                hotType: 'ONE_DAY'
            };
        },
        componentDidMount: function componentDidMount() {
            this.fetchData();

            // this.initIScroll();
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var _active = nextProps.active;
            var _el = document.getElementsByClassName('_wx_rank')[0];
            if (!_active) {
                _el.className = '_wx_rank cached';
                return false;
            } else {
                _el.className = '_wx_rank active';
            }
            return true;
        },
        componentDidUpdate: function componentDidUpdate() {
            if (this.props.active) {
                this.RAWIScroll && this.RAWIScroll.refresh();

                if (!this.props.detail_open) {
                    Share.setShare('');
                }
            }
        },
        fetchData: function fetchData(_params) {
            var self = this;
            if (!_params) {
                _params = params;
            }

            DataCenter.MHotModel.getData(_params, function (res) {
                console.log('CIModel is ready:');
                console.log(res);
                // if(res.movieBoxOfficeRanks.length === 0){
                //     self.loseMethod();
                // }
                // re-render
                self.setState({
                    model: res
                });
                setTimeout(self.initIScroll, 500);
            }, function (err) {
                console.log(err);
            });
        },
        initIScroll: function initIScroll() {
            var self = this;
            if (!this.RAWIScroll) {
                this.RAWIScroll = new IScroll('.wx-rank', {
                    scrollX: false,
                    scrollY: true,
                    mouseWheel: true,
                    bindToWrapper: true,
                    probeType: 3,
                    preventDefault: Util.iScrollClick(),
                    tap: Util.iScrollClick(),
                    click: Util.iScrollClick()
                });
            }
            var pullRefresh = new PullRefresh.refresh({
                scroll: this.RAWIScroll,
                model: DataCenter.MHotModel,
                params: params,
                callback: function callback(res) {
                    // re-render
                    self.setState({
                        model: res
                    });
                }
            });
            window.app_iScrolls[3] = this.RAWIScroll;
        },

        //
        onClickDays: function onClickDays(_hotType) {
            console.log(_hotType);
            //alert(_date);
            params.hotType = _hotType;
            this.setState({ hotType: _hotType });
            this.fetchData(params);

            if (_hotType === 'ONE_DAY') {
                Util.setPiaoFangEventGa('', 'WechatIndex', '24', '24h');

                // ga('send', 'event', '', 'click', '24' , 1);
                // //Hawkeye - Wepiao FE Analytics Solution
                // he && he('send', 'event', 'WxRank', 'click', '24h', 1);
            } else {
                Util.setPiaoFangEventGa('', 'WechatIndex', '7', '7d');

                // ga('send', 'event', '', 'click', '7' , 1);
                // //Hawkeye - Wepiao FE Analytics Solution
                // he && he('send', 'event', 'WxRank', 'click', '7d', 1);
            }
        }
    });

    return WxRank;
});
