define('pullRefresh',[], function(){
	'use strict'
	var isTouchend = false,
		isTouchHeight = false,
		isRefresh = false,
		isFetchData = false,
		classLoadName = '',
		model, params, self,
		isSlide = false;
	
	//
	var REFRESH = {
		height: 50,
		contentdown: '',
		contentover: '',
		contentrefresh: '...',
		CLASS_LOADING_DOWN: 'mui-pull-loading mui-icon icon-up2',
		CLASS_LOADING_SPINNER: 'mui-pull-loading mui-icon mui-spinner',
		pullLoading: 'mui-pull-loading',
		pullCaption: 'mui-pull-caption',
		transition: '-webkit-transform 0.3s ease-in',
		transformRotate: 'rotate(0deg)',
		transformRotate180: 'rotate(180deg)'
	};

	// touch
	document.addEventListener('touchstart', function (e) {
		isTouchend = false;
		classLoadName = REFRESH.CLASS_LOADING_DOWN;
	}, false);
	document.addEventListener('touchend', function (e) { 
		isTouchend = true;
		if(isTouchHeight){
			classLoadName = REFRESH.CLASS_LOADING_SPINNER;
		}
	}, false);

	function _refresh(options){
		self = this;
		this.refresh = REFRESH;
		this.options = options;
		this.model = options.model;
		this.params = options.params;
		this.myScroll = options.scroll;
		//
		this.isFirstSlide = false;

		this.refresh.contentdown = window._language && window._language.util.pullRefresh.th1;
		this.refresh.contentover = window._language && window._language.util.pullRefresh.th2;
		this.refresh.contentrefresh = window._language && window._language.util.pullRefresh.th3;

		this.init();
		this.initPulldownRefresh();
		this.initPulldownRefreshEvent();
		this.callback = options.callback;
		// window.myScroll = this.myScroll;
	}

	_refresh.prototype.init = function(){
		
		
	}
	_refresh.prototype.initPulldownRefresh = function(){
		this.pulldown = true;
		this.pullLoading = document.getElementsByClassName(this.refresh.pullLoading)[0];
		this.pullCaption = document.getElementsByClassName(this.refresh.pullCaption)[0];
		if (this.pullLoading) {
			this.pullCaption.innerHTML = this.refresh.contentdown;
			this.pullLoading.style.webkitTransition = this.refresh.transition;
			this.pullLoading.style.webkitTransform = this.refresh.transformRotate;
		}
	}

	_refresh.prototype.initPulldownRefreshEvent = function(){
		var _y,
			self = this,
			transformRotate = this.refresh.transformRotate,
			transformRotate180 = this.refresh.transformRotate180,
			contentdown = this.refresh.contentdown,
			contentover = this.refresh.contentover,
			contentrefresh = this.refresh.contentrefresh,
			pullLoading = this.pullLoading,
			pullCaption = this.pullCaption,
			class_down = this.refresh.CLASS_LOADING_DOWN,
			class_spinner = this.refresh.CLASS_LOADING_SPINNER,
			contenttxt;

        this.myScroll.on('scroll', function(){
        	_y = this.y>>0;

            if(isTouchend && isTouchHeight){
            	isRefresh = true;
            	if(_y <= 50){
            		this.scrollTo(0, 50);
            		classLoadName = class_spinner;
            		return;
            	}

            }
            // console.log('scroll:', _y)
            if(_y <= 0){
            	// this.scrollTo(0, 50);
            	isSlide = false;
            	isTouchHeight = false;
            	classLoadName = class_down;
            	self.resetPullDownLoading(pullLoading, pullCaption, contentdown, transformRotate, classLoadName);
            }else if(_y > 0 && _y < self.refresh.height){
            	
            	isTouchHeight = false;
            	if(isFetchData){
            		contenttxt = contentdown;
            		classLoadName = class_spinner;
            	}
            	if(isRefresh){
            		contenttxt = contentrefresh;
            	}else{
            		contenttxt = contentdown;
            	}
            	self.resetPullDownLoading(pullLoading, pullCaption, contenttxt, transformRotate, classLoadName);
            }else if(_y >= self.refresh.height){
            	
            	isTouchHeight = true;
            	if(isFetchData){
            		contenttxt = contentdown;
            		classLoadName = class_spinner;
            	}
            	if(isRefresh){
            		contenttxt = contentrefresh;
            	}else{
            		contenttxt = contentover;
            	}
            	self.resetPullDownLoading(pullLoading, pullCaption, contenttxt, transformRotate180, classLoadName);
            }

        })
		
		this.myScroll.on('scrollEnd', function(){
			// 
			if(!self.isFirstSlide && _y < 0){
				self.isFirstSlide = true;
				he && he('send', 'event', 'slide', 'click', 'slide', 1);
			}
			if(_y < 0) return;
			if(isFetchData) return;
			if(!isTouchHeight) return;
			this.scrollTo(0, 50);
			self.fetchData(self.params);
			isSlide = true;
			
		})
	}

	_refresh.prototype.resetPullDownLoading = function(pullLoading, pullCaption, txt, rotate, _className) {
		if (pullLoading && pullCaption) {
			pullCaption.innerHTML = txt;
			pullLoading.style.webkitTransform = rotate;
			pullLoading.className = _className;
		}

	}
	_refresh.prototype.fetchData = function(_params){
		if(isFetchData) return;
        if(!_params) return;
        var self = this;

        _params.isRefresh = true;
        isFetchData = true;

        this.model && this.model.getData(_params, function(res){
            console.log(res);

            setTimeout(function(){
            	isRefresh = isTouchend = isFetchData = isTouchHeight = false;

            	self.callback && self.callback(res);
            }, 500)
            isTouchend = isTouchHeight = false;
            self.myScroll.scrollTo(0, 0, 300, IScroll.utils.ease.quadratic)
        },function(err){
            console.log(err);
        }, true)
    }

    var PullRefresh = {
    	refresh: _refresh
    }

    return PullRefresh;
	
});
