/**
 *  Tab Bar.jsx
 *   Tab Bar
    <div className={ _current==4?"current":"" }
         onClick={_onClick.bind(null, 4)}>
        <i className={"icon-my " +  (_current==4?"current":"")}></i>
    </div>

 *
 */

'use strict';

define('tabBar',['React', 'util'], function (React, Util) {
    'use strict';

    var TabBar = React.createClass({
        displayName: 'TabBar',

        render: function render() {
            var _current = this.props.current;
            var _onClick = this.props.onTabClick;
            var isNewRank = localStorage.getItem('new_rank'),
                iRankhtml = React.createElement('i', { className: 'icon-rank ' + (_current == 3 ? 'current' : '') }),
                language = this.props.language;
            // if(!isNewRank){
            //     iRankhtml = <i className={"icon-rank " +  (_current==3?"current":"")}  data-title=""></i>;;
            // }
            var apps = this.state.apps,
                setting = '';
            // ios version > 1.1    android version > 1.2

            if (apps.isApp && (apps.type == 'iOS' && apps.version > 1.1 || apps.type == 'Android' && apps.version > 1.2)) {

                setting = React.createElement(
                    'div',
                    { className: _current == 4 ? 'current' : '',
                        onClick: this.handleSetingClick },
                    React.createElement('i', { className: 'icon-my ' + (_current == 4 ? 'current' : '') }),
                    language.mine.title
                );
            }

            return React.createElement(
                'footer',
                null,
                React.createElement(
                    'div',
                    { className: _current == 0 ? 'current' : '',
                        onClick: _onClick.bind(null, 0) },
                    React.createElement('i', { className: 'icon-boxoffice ' + (_current == 1 ? 'current' : '') }),
                    language.boxoffice.title
                ),
                React.createElement(
                    'div',
                    { className: _current == 1 ? 'current' : '',
                        onClick: _onClick.bind(null, 1) },
                    React.createElement('i', { className: 'icon-schedule ' + (_current == 0 ? 'current' : '') }),
                    language.schedule.title
                ),
                React.createElement(
                    'div',
                    { className: _current == 3 ? 'current' : '',
                        onClick: _onClick.bind(null, 3) },
                    iRankhtml,
                    language.rank.title
                ),
                setting
            );
        },
        getInitialState: function getInitialState() {
            return {
                apps: Util.compareVersion()
            };
        },
        handleSetingClick: function handleSetingClick() {
            //this.props.callbackSeting(true)
            location.hash = '/mine';
        }
    });

    return TabBar;
});
