

define('share',[
    "WxBridge",
    "util"
],function(
    WxBridge,
    Util
){
    'use strict'
    var share_img_url = "http://piaofang.wepiao.com/icon/logo180.png";
    var _desc = "piaofang.wepiao.com";
    var _is_wx = Util.is_weixn()
    var Share = {
        setShare: function(_title){
            if(_is_wx){
                WxBridge.share({
                  'imgUrl' : share_img_url, // 
                  'link' : window.location.href, // 
                  'title' : _title, // 
                  'desc' : _desc // 
                });
            }
        }
    }


    return Share;
});
