'use strict';

''; /**
    *  CommonTable.jsx
    *  
    *
    *  @props  {Null}      color           
    *  @props  {Null}      formatted       
    *  @props  {Null}      percent         
    *  @props  {Null}      showday         
    *  @props  {Null}      cinemaNum       
    *  @props  {Null}      index           
    *  @props  {Function}  helper           tr1  helper 
    *  @return {Component} CommonTable
    */

define('commonTable',['React', 'util'], function (React, Util) {
    'use strict';

    // Const
    var BARCOLORS = ['f8d303', '31d197', 'f76606', '0ea9fa', 'e83295', '9badc1', 'b3c1d1', 'c6d1dd', 'd7dfe8', 'E5E8EA', 'ecf0f4', 'F3F3F3', 'F5F5F5', 'F7F7F5', 'F7F8F8', 'F7F8F8'];

    // for lynn's
    localStorage.setItem('language', 'en');
    var localLanguage = localStorage.getItem('language'),
        _languageType = '';
    if (localLanguage) {
        _languageType = localLanguage;
    } else {
        _languageType = navigator.language.toLowerCase() == 'zh-cn' ? 'cn' : 'en';

        _languageType = 'en';
    }

    // Component
    var CommonTable = React.createClass({
        displayName: 'CommonTable',

        render: function render() {
            var _this = this;

            var _announcement = '',
                table_rank = '';

            if (this.props.toast) {
                _announcement = React.createElement(
                    'p',
                    { className: 'announcement css-1px' },
                    React.createElement('i', { className: 'icon-sound' }),
                    this.props.toast
                );
            }
            if (this.props.movieList) {
                var _movieItems = this.props.movieList.map(function (movie, i) {

                    // table need Color
                    if (_this.props.color) {
                        var _barColor = {
                            backgroundColor: '#' + BARCOLORS[i]
                        };
                        var _color = React.createElement('i', { style: _barColor });
                    } else {
                        var _color = '';
                    }

                    // process Table Row 1.
                    var _tr1 = movie[_this.props.tr1_field];
                    if (!_tr1 && _languageType == 'en') {
                        _tr1 = 'others';
                    }
                    if (_this.props.helper) {
                        _tr1 = _this.props.helper(_tr1);
                    }
                    // process special showday tag (tr1)
                    if (_this.props.showday) {
                        if (movie.showDays === 1 && !_this.props.isLong) {
                            _tr1 = Util.strShort(_tr1, 7);
                        } else {
                            _tr1 = Util.strShort(_tr1, 11);
                        }
                        if (_languageType == 'cn') {
                            if (movie.showDays === 0) var _showday = React.createElement(
                                'em',
                                null,
                                ' '
                            );else if (movie.showDays === 1) var _showday = React.createElement(
                                'em',
                                null,
                                ' '
                            );else if (movie.showDays < 0) var _showday = React.createElement(
                                'em',
                                null,
                                ' '
                            );else var _showday = '';

                            if (movie.scheduleDate && _this.props.isPresell) {

                                var scheduleDate = new Date(movie.scheduleDate);
                                var _days = Util.restrictSwitchToDate1(Util.getTodayStr(), scheduleDate);
                                // alert(movie.scheduleDate.replace('T00:00:00', ' '))
                                if (_days > 0) {
                                    _showday = React.createElement(
                                        'em',
                                        { style: { 'color': '#f76606' } },
                                        ' '
                                    );
                                }
                            }
                        } else {
                            if (movie.showDays === 0) var _showday = React.createElement(
                                'em',
                                null,
                                ' Midnight'
                            );else if (movie.showDays === 1) var _showday = React.createElement(
                                'em',
                                null,
                                ' Premiere'
                            );else if (movie.showDays < 0) var _showday = React.createElement(
                                'em',
                                null,
                                ' Limited release'
                            );else var _showday = '';

                            if (movie.scheduleDate && _this.props.isPresell) {
                                var scheduleDate = new Date(movie.scheduleDate);
                                var _days = Util.restrictSwitchToDate1(Util.getTodayStr(), scheduleDate);
                                if (_days > 0) {
                                    _showday = React.createElement(
                                        'em',
                                        { style: { 'color': '#f76606' } },
                                        ' Presell'
                                    );
                                }
                            }
                        }
                    }

                    if (_tr1 === '' || _tr1 === 'others') {
                        _showday = '';
                    }

                    if (_tr1.indexOf(' Cinemas') > 0) {
                        _tr1 = _tr1.replace(' Cinemas', '');
                    } else if (_tr1.indexOf(' Cinema') > 0) {
                        _tr1 = _tr1.replace(' Cinema', '');
                    }

                    // process cinemaNum tag (tr1)
                    if (_this.props.cinemaNum) {
                        if (movie.cinemas) var _cinemaNum = React.createElement(
                            'small',
                            null,
                            '',
                            movie.cinemas,
                            _languageType == 'cn' ? '' : '',
                            ''
                        );else var _cinemaNum = '';
                    }

                    // process index
                    if (_this.props.index) {
                        var _index = React.createElement(
                            'span',
                            { className: 'cinema-index' },
                            i + 1
                        );
                    } else var _index = '';

                    // support both movie and cinema
                    //
                    var temp_span = '',
                        release_date = '',
                        all_start = '',
                        span_end = '',
                        _release = _languageType == 'cn' ? '' : ' Release';

                    if (_this.props.temp_field) {
                        var productTickets = _languageType == 'cn' ? parseInt(movie[_this.props.temp_field] / 10000) : movie[_this.props.temp_field].replace('M', '');
                        temp_span = React.createElement(
                            'span',
                            { className: 'middle m-middle' },
                            productTickets
                        );
                        release_date = React.createElement(
                            'p',
                            null,
                            movie['releaseDate'].replace('T00:00:00', '') + _release
                        );
                        all_start = 'all-start';
                        table_rank = 'table-rank years-rank';

                        _tr1 = React.createElement(
                            'h5',
                            null,
                            _tr1
                        );
                    }

                    if (_this.props.name_field == 'FirstDayRankName' || _this.props.name_field == 'DaysRankName') {

                        var _date = movie[_this.props.tr3_field].replace('T00:00:00', '');
                        var _week = _languageType == 'cn' ? Util.getDay(_date) : Util.getDayEn(_date);
                        span_end = React.createElement(
                            'span',
                            { className: 'end m-middle' },
                            _date,
                            React.createElement(
                                'p',
                                null,
                                _week
                            )
                        );
                        if (_this.props.name_field == 'DaysRankName') {
                            table_rank = 'table-rank days-rank';
                            release_date = React.createElement(
                                'p',
                                null,
                                movie['releaseDate'].replace('T00:00:00', '') + _release
                            );
                        } else {
                            table_rank = 'table-rank firstday-rank';
                        }

                        _tr1 = React.createElement(
                            'h5',
                            null,
                            _tr1
                        );
                        movie[_this.props.tr2_field] = parseInt(movie[_this.props.tr2_field]);
                    } else {
                        span_end = React.createElement(
                            'span',
                            { className: 'end' },
                            _this.props.formatted ? Util.getFormattedNum(movie[_this.props.tr3_field]) : movie[_this.props.tr3_field],
                            _this.props.percent ? '%' : ''
                        );
                    }

                    return React.createElement(
                        'li',
                        { key: i,
                            className: _this.props.onItemClick && 'hasTouchState',
                            onClick: _this.handleClick.bind(null, movie[_this.props.id_field], _tr1)
                        },
                        React.createElement(
                            'span',
                            { className: 'start ' + all_start },
                            _index,
                            _color,
                            _tr1,
                            _showday,
                            _cinemaNum,
                            release_date
                        ),
                        React.createElement(
                            'span',
                            { className: 'middle' },
                            _this.props.formatted ? Util.getFormattedNum(movie[_this.props.tr2_field]) : _this.props.temp_field ? parseInt(movie[_this.props.tr2_field]) : movie[_this.props.tr2_field]
                        ),
                        temp_span,
                        span_end
                    );
                });
            } else {
                var _movieItems = React.createElement(
                    'li',
                    null,
                    ' ... '
                );
            }

            //
            var temp_nav = '';
            if (this.props.temp_th) {
                temp_nav = React.createElement(
                    'span',
                    { className: 'middle m-middle' },
                    this.props.temp_th
                );
            }

            return React.createElement(
                'div',
                { className: 'common-table ' + table_rank },
                _announcement,
                React.createElement(
                    'div',
                    { className: 'table-header css-1px' },
                    React.createElement(
                        'span',
                        { className: 'start' },
                        this.props.th1
                    ),
                    React.createElement(
                        'span',
                        { className: 'middle' },
                        this.props.th2
                    ),
                    temp_nav,
                    React.createElement(
                        'span',
                        { className: 'end' },
                        this.props.th3
                    )
                ),
                React.createElement(
                    'ul',
                    { className: 'border-1px' },
                    _movieItems
                )
            );
        },
        handleClick: function handleClick(id, name) {
            // block movieName = ""
            if ((name == '' || name == 'others') && id == 0) {
                return;
            }
            if (this.props.onItemClick) {
                this.props.onItemClick(id, name);
            }
        },
        getDefaultProps: function getDefaultProps() {
            return {
                tr1_field: 'movieName',
                tr2_field: 'productBoxOffice',
                tr3_field: 'productTotalBoxOffice',
                th1: '',
                th2: '()',
                th3: '',
                id_field: 'movieId',
                name_field: 'movieName'
            };
        }
    });

    return CommonTable;
});
