'use strict';

define('comingMovie',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'navBar', 'util', 'comingMovieTable', 'datacenter', 'commonTable', 'SelectCity', 'SelectDate', 'TimeZone', 'feedbackBlock', 'share', 'pullRefresh'], function (React, Chart, ReactChart, IScroll, Router, NavBar, Util, ComingMovieTable, DataCenter, CommonTable, SelectCity, SelectDate, TimeZone, FeedbackBlock, Share, PullRefresh) {
    'use strict';
    // Component
    var ComingMovie = React.createClass({
        displayName: 'ComingMovie',

        // pass through funciton
        getInitialState: function getInitialState() {
            return {
                toggle: 'date',
                language: null,
                params: {
                    'paging': {
                        'page': 1, // 
                        'pageSize': 50 // 
                    }
                }
            };
        },
        componentDidMount: function componentDidMount() {
            var language = this.props.language;
            this.setState({ language: language });
        },
        onBackBtnClick: function onBackBtnClick() {
            // manually judge the hashchange.
            var oldURL = location.href;
            setTimeout(function () {
                var newURL = location.href;
                if (oldURL === newURL) {
                    console.log('hey you must on the history stack bottom, let me send you to the home.');
                    //this.router.setRoute("/boxoffice")
                    // Android
                    location.href = '#/boxoffice';
                }
                if (location.hash === '') {
                    location.hash = '/boxoffice';
                }
            }, 100);

            window.history.back(1);
        },
        selectDate: function selectDate() {
            if (this.state.toggle != 'date') {
                this.setState({
                    toggle: 'date',
                    params: {
                        'paging': {
                            'page': 1, // 
                            'pageSize': 50 // 
                        }
                    }
                });
            }
        },
        selectCount: function selectCount() {
            if (this.state.toggle != 'count') {
                this.setState({
                    toggle: 'count',
                    params: {
                        'paging': {
                            'page': 1, // 
                            'pageSize': 50 // 
                        },
                        'movieFilter': {
                            'sortColumn': 'wantCount',
                            'sortType': 'desc'
                        }
                    }
                });
            }
        },
        setPiaoFang: function setPiaoFang() {
            ga('send', 'pageview', {
                'page': '/comingmovie',
                'title': 'coming'
            });
            //Hawkeye - Wepiao FE Analytics Solution
            he && he('send', 'pageview', 'comingmovie');
        },
        render: function render() {
            var that = this,
                state = that.state,
                toggle = state.toggle,
                language = state.language,
                tabDate = 'tab-date',
                tabWant = 'tab-want';
            if (!language) {
                language = that.props.language;
                console.log(language);
            }
            if (toggle == 'date') {
                tabDate += ' active';
            } else {
                tabWant += ' active';
            }
            return React.createElement(
                'div',
                null,
                React.createElement(NavBar, { title: language.comingFilm.title, leftNav: ' ', rightLogo: true, backCallback: this.onBackBtnClick }),
                React.createElement(
                    'div',
                    { className: 'coming-wrapper' },
                    React.createElement(
                        'div',
                        { className: 'coming-tab' },
                        React.createElement(
                            'div',
                            { className: tabDate, onClick: that.selectDate.bind(null) },
                            React.createElement(
                                'span',
                                null,
                                language.comingFilm.item_tab.releaseDate
                            ),
                            React.createElement('i', { className: 'icon-down' })
                        ),
                        React.createElement(
                            'div',
                            { className: tabWant, onClick: that.selectCount.bind(null) },
                            React.createElement(
                                'span',
                                null,
                                language.comingFilm.item_tab.wantCount
                            ),
                            React.createElement('i', { className: 'icon-down' })
                        )
                    ),
                    React.createElement(ComingMovieTable, { params: state.params, language: language.comingFilm.item_info })
                )
            );
        }
    });

    return ComingMovie;
});
