define('util',[],function(){
    'use strict'
    // console.log('module util loaded..');
    String.format = function () {
        if (arguments.length == 0)
            return null;
        var str = arguments[0];
        for (var i = 1; i < arguments.length; i++) {
            var re = new RegExp('\\{' + (i - 1) + '\\}', 'gm');
            str = str.replace(re, arguments[i]);
        }
        return str;
    }

    String.transform = function (template, value) {
        template = template || '';
        value = value || {};

        // : ${my-param}
        var reg = /\$\{([^{}]*)}/g;

        // 
        var replace = function (str, match) {
            return typeof value[match] === 'string' || typeof value[match] === 'number' ? value[match] : str;
        };

        return template.replace(reg, replace);
    };

    var Util = {

        /**
         * @param {Date}
         * @return {String} 
         */
        getDateStr: function(_date){
            var _y = _date.getFullYear(),
                _m = _date.getMonth() + 1,    //  0
                _m = _m < 10 ? "0" + _m : _m,
                _d = _date.getDate(),
                _d = _d < 10 ? "0" + _d : _d,
                _s = _y + "-" + _m + "-" + _d;
            return _s;
        },

        //
        getTodayStr: function(_format){
            var _date = new Date();

            if (_format && _format == "y-d-zh"){
                var _str =
                    (_date.getMonth()+1) + "" +
                    (_date.getDate()) +""

                return _str;
            } else {
                return this.getDateStr(_date);
            }

        },

        /**
         * @param {String} _dateStr
         * @return {String}  X  X 
         */
        getChineseDateStr: function(_dateStr){
            var _date = new Date(_dateStr);
            var _m = _date.getMonth() + 1,    //  0
                _m = _m < 10 ? "0" + _m : _m,
                _d = _date.getDate(),
                _d = _d < 10 ? "0" + _d : _d,
                _s = _m + "-" + _d;
            // var _str =
            //     (_date.getMonth()+1) + "" +
            //     (_date.getDate()) +""

            return _s;
        },
        getEnglishDateStr: function(_dateStr){
            var _date = new Date(_dateStr);
            var _m = _date.getMonth() + 1,    //  0
                _m = _m < 10 ? "0" + _m : _m,
                _d = _date.getDate(),
                _d = _d < 10 ? "0" + _d : _d,
                _s = _m + "-" + _d;

            return _s;
        },
        getChineseDateStrWithDay: function(_dateStr){
            var _date = new Date(_dateStr);
            return Util.getDateStr(_date) + ' ' + Util.getDay(_dateStr)
            // var _str =
            //     (_date.getFullYear()) + "-" +
            //     (_date.getMonth()+1) + "-" +
            //     (_date.getDate()) + " " +
            //     Util.getDay(_dateStr)

             // _str;
        },

        getEnglishDateStrWithDay: function(_dateStr){
            var _date = new Date(_dateStr);
            // var _str =
            //     //(_date.getFullYear()) + "-"+
            //     (_date.getMonth()+1) + "-" +
            //     (_date.getDate()) + " " +
            //     Util.getDayEn(_dateStr)

            // return _str;
            return Util.getDateStr(_date) + ' ' + Util.getDayEn(_dateStr)
        },

        getChineseDateStrWithYear: function(_dateStr){
            var _date = new Date(_dateStr);
            var _str =
                (_date.getFullYear()) + "" +
                (_date.getMonth()+1) + "" +
                (_date.getDate()) +""

            return _str;
        },

        /**
         * @param {String} 
         * @return {String} 
         */
        getDay: function(_dateStr){
            var _date = new Date(_dateStr),
            _day = _date.getDay(),
            _dayMap = ["", "", "", "", "", "", ""];

            return _dayMap[_day];
        },

        getDayEn: function(_dateStr){
            var _date = new Date(_dateStr),
            _day = _date.getDay(),
            _dayMap = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

            return _dayMap[_day];
        },

        getFormattedNum: function(num){
            // make sure it is String
            return String(num).replace(/\B(?=(?:\d{3})+\b)/g, ',') ;
        },

        getHundredMillion: function(num, _units, numstr){
            if(_units == ''){
                if(num >= 10000){
                    return {num: (num / 10000).toFixed(2), units: ''}
                }else{
                    return {num: num.toFixed(1), units: _units}
                }
                
            }else{
                
                if(numstr && numstr.toString().indexOf('M') > 0){
                    _units = 'M';
                }else{
                    _units = '';
                }
                return {num: num, units: _units}
            }
        },

        // used in every initIScroll() !
        iScrollClick: function(){
            if (/iPhone|iPad|iPod|Macintosh/i.test(navigator.userAgent)) return false;
            if (/Chrome/i.test(navigator.userAgent)) return (/Android/i.test(navigator.userAgent));
            if (/Silk/i.test(navigator.userAgent)) return false;
            if (/Android/i.test(navigator.userAgent))
            {
              var s=navigator.userAgent.substr(navigator.userAgent.indexOf('Android')+8,3);
              return parseFloat(s[0]+s[3]) < 44 ? false : true
            }
        },

        // 
        prevDay: function(date){
            var mydate = new Date(date);
            var resultdate = new Date(mydate - 1 - 1*24*60*60*1000);
            return this.getDateStr(resultdate);
        },

        // 
        nextDay: function(date){
            var mydate = new Date(date);
            var resultdate = new Date(mydate - 1 + 1*24*60*60*1000);
            return this.getDateStr(resultdate);
        },

        // dateindex
        getIndexDaysStr: function(date, index){
            var mydate = new Date(date);
            var resultdate = new Date(mydate - 1 + index*24*60*60*1000);
            return this.getDateStr(resultdate);
        },

        // 
        isEqualDays: function(day1, day2){
            var _day1 = new Date(day1);
            var _day2 = new Date(day2);
            var _y1 = _day1.getFullYear(),
                _m1 = _day1.getMonth(),
                _d1 = _day1.getDate(),
                _y2 = _day2.getFullYear(),
                _m2 = _day2.getMonth(),
                _d2 = _day2.getDate();
            if(_y1 == _y2 && _m1 == _m2 && _d1 == _d2){
                return true;
            }else{
                return false;
            }
        },

        /*toast
            @txt  
            @delay  toast
        */
        toast: function(txt, delay){
            var _toast = document.getElementById("toast");
            if(_toast){
                delay = delay || 3e3;
                _toast.innerHTML = txt;
                _toast.style.display = "block";
                _toast.style.opacity = 0;
                setTimeout(function(){
                    _toast.style.opacity = 1;
                }, 50);
                
                setTimeout(function(){
                    _toast.style.opacity = 0;
                    setTimeout(function(){
                        _toast.style.display = "none";
                    }, 1e3);
                }, delay)
            }
        },

        /*
            @_nowDate  
            @_selectDate  
        */
        restrictSwitchToDate: function(_nowDate, _selectDate){
            var nowDate = new Date(_nowDate) - 1 + 1;
            var selectDate = new Date(_selectDate) - 1 + 1;
            var _day = 1*24*60*60*1000;
            var _days;
            if(nowDate > selectDate){
                _days = -Math.floor((nowDate - selectDate) / _day);
            }else if(nowDate < selectDate){
                _days = Math.floor((selectDate - nowDate) / _day);
            }else{
                _days = 0;
            }
            return _days
        },

        restrictSwitchToDate1: function(_nowDate, _selectDate){
            var nowDate = new Date(_nowDate) - 1 + 1;
            var selectDate = new Date(_selectDate) - 1 + 1;
            var _day = 1*24*60*60*1000;
            var _days;
            if(nowDate > selectDate){
                _days = -Math.ceil((nowDate - selectDate) / _day);
            }else if(nowDate < selectDate){
                _days = Math.ceil((selectDate - nowDate) / _day);
            }else{
                _days = 0;
            }
            return _days
        },

        // , 12
        strShort: function(string, maxLength) {
            if (!string) return "";
            var len = 0,
                arr = string.split(""),
                result = [];
            maxLength = (maxLength || 12) * 2;

            var l = arr.length;
            for (var i = 0; i < l; ++i) {
                if (arr[i].charCodeAt(0) < 299) {
                    len++;
                } else {
                    len += 2;
                }
                result.push(arr[i]);

                //
                if (i == l - 2 && len <= maxLength - 2) {
                    result.push(arr[i + 1]);
                    break;
                } else if (len > maxLength - 2) {
                    result.push('...');
                    break;
                }
            }
            return result.join('');
        },

        //
        is_weixn: function(){
            var ua = navigator.userAgent.toLowerCase();
            if(ua.match(/MicroMessenger/i)=="micromessenger") {
                return true;
            } else {
                return false;
            }
        },

        compareSystem: function(){
            var u = navigator.userAgent;
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
            return isAndroid ? 'Android' : 'iOS';
        },

        /*
        app
        */
        compareVersion: function() {
            var ua = navigator.userAgent;
            var isIosVersion = ua.indexOf('Wepiao_iPhone_Piaofang'),
                isAndroidVersion = ua.indexOf('Wepiao_Android_Piaofang'),
                _htmls = ua.split('Wepiao_iPhone_Piaofang/'),
                versionStr, version;
            // UA  iOS
            if (isIosVersion >= 0) {
                _htmls = ua.split('Wepiao_iPhone_Piaofang/');
                if(_htmls && _htmls.length > 1){
                    versionStr = _htmls[1].split('/')[0];
                    version = versionStr * 1;
                }

                return {
                    isApp: true,
                    version: version,
                    type: 'iOS'
                }
            }
            // UA  Android
            if (isAndroidVersion >= 0) {
                _htmls = ua.split('Wepiao_Android_Piaofang/');
                if(_htmls && _htmls.length > 1){
                    versionStr = _htmls[1].split('/')[0];
                    version = versionStr * 1;
                }

                return {
                    isApp: true,
                    version: version,
                    type: 'Android'
                }
            }

            return {
                isApp: false,
                version: 0,
                type: ''
            }
        },

        //
        setPiaoFang: function(){
            var _hash = window.location.hash;
            if( _hash == '' ){
                ga('send', 'pageview', {
                    'page': '/boxoffice',
                    'title': ''
                });
                //Hawkeye - Wepiao FE Analytics Solution
                he && he('send', 'pageview', 'boxoffice');

                window.shareinfo = {
                    page: '-',
                    title: '-',
                    desc: 'http://piaofang.wepiao.com/#/boxoffice',
                    url:  'http://piaofang.wepiao.com/#/boxoffice',
                    height: 970
                }
            }
        },
        setPiaoFangEventGa: function(eventNameGa, eventNameHe, LabelGa, LabelHe){
            ga('send', 'event', eventNameGa, 'click', LabelGa , 1);
            he && he('send', 'event', eventNameHe, 'click', LabelHe, 1);
        },

        //location search
        getSearchHash: function (fragmentQueryString) {
            var queryString = fragmentQueryString || window.location.search;
            var search_array = queryString.replace('?', '').split('&'),
                search_hash = {};
            for (var i = search_array.length - 1; i >= 0; i--) {
                var search = search_array[i].split('=');
                search_hash[search[0]] = decodeURIComponent(search[1]);
            }
            return search_hash;
        },

        getSearchValue: function (key) {
            return Util.getSearchHash()[key];
        }

    }

   return Util;
})
;
