'use strict';

define('TimeZone',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'util'], function (React, Chart, ReactChart, IScroll, Router, Util) {
    'use strict';
    // data
    var timeZoneArr = ['', '', ''];
    var style = {
        background: 0,
        border: 0,
        fontSize: '1.22rem'
    };
    // 
    var navTimeEl;
    var defaultCss = 'nav_time';
    var currentCss = 'nav_time arr-solid-up';
    // 
    var openStatus = false;
    var TimeZone = React.createClass({
        displayName: 'TimeZone',

        render: function render() {
            var _handleClick = this.handleClick;
            var _date = this.state.date;
            timeZoneArr = window._language.util.timeZone.titles;
            return React.createElement(
                'div',
                { className: 'nav_time', onClick: _handleClick },
                React.createElement('i', { className: 'icon-duration' }),
                React.createElement(
                    'span',
                    { id: 'nav_time', style: style, className: 'nav_city  time-bucket' },
                    this.state.item
                ),
                React.createElement('span', { className: 'arr-down' })
            );
        },
        getInitialState: function getInitialState() {
            return {
                item: window._language.util.timeZone.titles[0]
            };
        },

        componentDidMount: function componentDidMount() {},

        componentDidUpdate: function componentDidUpdate() {},

        handleClick: function handleClick(event) {
            navTimeEl = event.currentTarget;
            var dialog = document.getElementById('dialog');
            if (openStatus) {
                navTimeEl.className = defaultCss;
                if (dialog) {
                    dialog.innerHTML = '';
                }
                openStatus = false;
            } else {
                navTimeEl.className = currentCss;
                openStatus = true;
                if (!dialog) {
                    dialog = document.createElement('p');
                    dialog.id = 'dialog';
                    dialog.style.height = '100%';
                    var app = document.getElementById('app');
                    app.appendChild(dialog);
                }

                React.render(React.createElement(TimeZoneDialog, {
                    isMovieDetail: this.props.isMovieDetail,
                    _top: this.props._top,
                    items: timeZoneArr,
                    klassName: this.props.klassName,
                    handleClick: this.handleLabClick,
                    removeDialog: this.removeDialog }), dialog);
            }

            this.props.showDailogCallback && this.props.showDailogCallback();
        },

        handleLabClick: function handleLabClick(_values) {
            var value = _values[0];
            var timeZoneName = _values[1];
            var _value = event.target.value;
            this.setState({ item: timeZoneName });
            this.props.timeZoneCallback(_value);
            this.removeDialog();

            Util.setPiaoFangEventGa('', 'selectperiod', timeZoneName, timeZoneName);

            // ga('send', 'event', '', 'click', timeZoneName, 1);
            // //Hawkeye - Wepiao FE Analytics Solution
            // he && he('send', 'event', 'selectperiod', 'click', timeZoneName, 1);
        },

        removeDialog: function removeDialog() {
            var dialog = document.getElementById('dialog');
            //var nav_time = document.getElementById("nav_time");
            if (dialog) {
                dialog.innerHTML = '';
            }
            if (navTimeEl) {
                navTimeEl.className = defaultCss;
            }
            openStatus = false;
            // if(navCityEl){
            //     navCityEl.className = 'nav_city';
            // }
        }
    });

    //
    var timeZoneStyleH = {
        height: '100%'
    };
    var ulStyle = {},
        divStyle = {};
    var TimeZoneDialog = React.createClass({
        displayName: 'TimeZoneDialog',

        render: function render() {
            var _handleClick = this.props.handleClick;
            var _handleClickDialogBack = this.props.removeDialog;
            var klassName = 'select-bucket ' + this.props.klassName;
            var items = this.props.items,
                len = items.length,
                _timeZoneItems,
                _handleChange = this.handleChange;

            if (this.props.isMovieDetail) {
                divStyle = {
                    top: '122px'
                };
                ulStyle = {
                    top: '0px'
                };
            } else {
                divStyle = {};
                ulStyle = {
                    top: '79px'
                };
            }

            if (items && len > 0) {
                _timeZoneItems = items.map(function (item, i) {
                    if (i === 1) {
                        return React.createElement(
                            'li',
                            { value: i, onClick: _handleClick.bind(null, [i, item]) },
                            item,
                            React.createElement(
                                'mark',
                                null,
                                ' (18:30-22:00)'
                            )
                        );
                    } else {
                        return React.createElement(
                            'li',
                            { value: i, onClick: _handleClick.bind(null, [i, item]) },
                            item
                        );
                    }
                });
            } else {
                return;
            }

            return React.createElement(
                'div',
                { style: timeZoneStyleH },
                React.createElement('div', { className: 'dialog_back', onClick: _handleClickDialogBack }),
                React.createElement(
                    'div',
                    { className: 'citylist-wrap detailpage-citylist', style: divStyle },
                    React.createElement('div', { className: 'dialog_back dialog_back2', onClick: _handleClickDialogBack }),
                    React.createElement(
                        'div',
                        { className: 'subview fixed-box citypicker-wrapper' },
                        React.createElement(
                            'ul',
                            { className: klassName, style: ulStyle },
                            _timeZoneItems,
                            ' '
                        )
                    )
                )
            );
        }
    });

    return TimeZone;
});

