define('WxBridge',[
    'lib/reqwest.min',
    "WX"
], function(
    reqwest,
    wx
) {
    'use strict'

    //
    function wx_verif(force,debug){
        var _force = force,_debug = debug;
        // var   verif_Url = ['http://wx.wepiao.com','http://yx.wepiao.com','http://weixin.wepiao.com'];//, 'http://piaofang.wepiao.com'
        // if(verif_Url.indexOf(window.location.origin) == -1){
        //     //alert(",");
        //     return;
        // }
        getcap(_force,_debug);
    }

    function getcap(_force,_debug){
        //alert(window.location.href);
        reqwest({
            // type : "get",
            // async:true,
            // data:{
            //     url: window.location.href,
            //     force:_force
            // },
            // url : "http://182.254.230.26:8080/CreateJsApiTicket.php",
            // dataType : "jsonp",
            url: "http://182.254.230.26:8080/CreateJsApiTicket.php",
            data: {
                url: window.location.href,
                force:_force
            },
            type: 'jsonp',
            contentType: 'application/jsonp',
            method: 'post',
            crossOrigin: true,
            success : function(res) {
                if(res.ret == 0){
                    var data = res.data;
                    //alert(JSON.stringify(data));
                    //alert(_debug);
                    wx.config({
                        debug: _debug,//true //,apialertpclogpc
                        appId:data.appId, // 
                        timestamp:data.timestamp , // 
                        nonceStr:data.nonceStr, // 
                        signature:data.signature,// 
                        jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'] // 
                    });

                    if(_force == 1){
                        share(share_param);
                    }

                    wx.error(function(res){
                        //alert(JSON.stringify(res));
                        //
                        if(res.errMsg != 'config:ok'){//
                            console.log("");
                            wx_verif(1,false);
                        }
                    });
                }
            },
            error : function() {
                if(_force == 1){
                    share(share_param);
                }
            }
        });
    }

    //
    function share(param){
        var _param = {
            title : param.title || '',// 
            link : param.link || '',// 
            imgUrl : param.imgUrl || '',// 
            desc : param.desc || '',// ,
            type : param.type || 'link',// ,musicvideolinklink,
            dataUrl : param.dataUrl || '', // typemusicvideo,
            callback: param.callback || function (){}//
        };

        //alert(JSON.stringify(_param));
        wx.ready(function(res){
            //  wx.hideAllNonBaseMenuItem();
            //alert('wx ready');
            wx.showOptionMenu({
                menuList: [
                    'menuItem:share:appMessage',
                    'menuItem:share:timeline'
                ]
            });

            //wx.hideMenuItems({
            //    menuList: ['menuItem:copyUrl','menuItem:openWithSafari','menuItem:share:brand'] // menu3
            //});
            //
            wx.checkJsApi({
                jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage','hideMenuItems'],
                success: function(res) {
                    if((res.checkResult.onMenuShareTimeline=!!false) || (res.checkResult.onMenuShareAppMessage=!!false)){
                        return false;
                    }
                }
            });
            //
            wx.onMenuShareTimeline({
                title : _param.title,
                desc : _param.desc,
                link : _param.link,
                imgUrl : _param.imgUrl,
                success : function (res) {
                    // 
                    _param.callback();

                },
                cancel: function (res) {

                    // 
                }
            });
            //
            wx.onMenuShareAppMessage({
                title : _param.title,
                desc : _param.desc,
                link : _param.link,
                imgUrl : _param.imgUrl,
                type : _param.type,
                dataUrl : _param.dataUrl,
                success : function (res) {
                    // 
                    _param.callback();


                },
                cancel: function (res) {

                    // 
                }
            });
        });
    }

    wx_verif(0, false);

    var WxBridge = {

        auth: wx_verif,
        share: share

    }
    return WxBridge;

})
;
