/**
 *  search.jsx
 */

'use strict';

define('Search',['React', 'datacenter', 'IScroll', 'util'], function (React, DataCenter, IScroll, Util) {
    'use strict';
    var _hide = { 'display': 'none;' };
    var _show = { 'display': 'block;' };
    var inputEl,
        searchHistorys = [];
    var isFisrtReload = true;
    var language;

    // <i className="ico-close"></i>
    //
    var params = {
        'name': '',
        'moviePaging': {
            'page': 1,
            'pageSize': 200
        },
        'cinemaPaging': {
            'page': 1,
            'pageSize': 200
        }
    };

    var Search = React.createClass({
        displayName: 'Search',

        render: function render() {

            language = this.props.language.search;
            var _isOpen = this.props.open ? 'open' : '',
                backCallback = this.gotoBack,
                keySearch = this.keySearch,
                model = this.state.model,
                searchMovieList = '',
                searchCinemaList = '',
                cinemas = null,
                movies = null,
                cinemasTotal = 0,
                moviesTotal = 0,
                _searchHistory = this.getLocalStorageSearchHistory(),
                noMovieResult,
                noCinemaResult;

            if (model) {
                cinemas = model.cinemas;
                movies = model.movies;
            }
            if (movies && movies.length > 0) {
                moviesTotal = model.moviePaging.total;
                var movieList = this.getLiList(movies, 'movie', language);

                searchMovieList = React.createElement(
                    'div',
                    { className: 'search-list' },
                    React.createElement(
                        'h4',
                        null,
                        moviesTotal,
                        language.movie.title
                    ),
                    React.createElement(
                        'div',
                        { className: 'm-list-title' },
                        React.createElement(
                            'span',
                            null,
                            language.movie.th1
                        ),
                        React.createElement(
                            'span',
                            null,
                            language.movie.th2
                        )
                    ),
                    React.createElement(
                        'ul',
                        { className: 'm-list' },
                        movieList
                    )
                );
                noMovieResult = false;
            } else {
                noMovieResult = true;
            }
            if (cinemas && cinemas.length > 0) {
                cinemasTotal = model.cinemaPaging.total;
                var cinemasList = this.getLiList(cinemas, 'cinema', language);
                searchCinemaList = React.createElement(
                    'div',
                    { className: 'search-list' },
                    React.createElement(
                        'h4',
                        null,
                        cinemasTotal,
                        language.cinema.title
                    ),
                    React.createElement(
                        'div',
                        { className: 'm-list-title' },
                        React.createElement(
                            'span',
                            null,
                            language.cinema.th1
                        ),
                        React.createElement(
                            'span',
                            null,
                            language.cinema.th2
                        )
                    ),
                    React.createElement(
                        'ul',
                        { className: 'm-list' },
                        cinemasList
                    )
                );
                noCinemaResult = false;
            } else {
                noCinemaResult = true;
            }

            return React.createElement(
                'div',
                { className: 'movie-detail _search ' + _isOpen },
                React.createElement(
                    'div',
                    { className: 'search' },
                    React.createElement(
                        'div',
                        { className: 'search-wrap' },
                        React.createElement(
                            'div',
                            { className: 'input-box input-placeholder' },
                            React.createElement(
                                'label',
                                null,
                                React.createElement('i', { className: 'icon-search' })
                            ),
                            React.createElement('input', { type: 'text', onKeyUp: keySearch, onChange: keySearch, placeholder: language.info[0] }),
                            React.createElement(
                                'i',
                                { className: 'ico-clear', style: { 'display': 'none;' } },
                                ''
                            )
                        ),
                        React.createElement(
                            'a',
                            { className: 'cancle', onClick: this.gotoBack.bind() },
                            language.info[1]
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'search-full' },
                    React.createElement(
                        'div',
                        { className: 'search-scroll' },
                        searchMovieList,
                        searchCinemaList,
                        React.createElement(
                            'div',
                            { className: 'search-no ' + (noMovieResult && noCinemaResult ? '' : 'cached') },
                            React.createElement(
                                'p',
                                null,
                                isFisrtReload ? language.info[3] : language.info[2]
                            )
                        ),
                        _searchHistory
                    )
                )
            );
        },
        /**/
        getInitialState: function getInitialState() {
            return {
                model: null,
                isHistory: true,
                searchHistorys: localStorage.getItem('data_searchHistory')
            };
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var open = nextProps.open,
                _el = document.getElementsByClassName('_search')[0];
            if (open) {
                _el.className = 'movie-detail _search open';
                return true;
            } else {
                _el.className = 'movie-detail _search';
                return false;
            }
        },
        fetchData: function fetchData(_params) {
            var self = this;
            if (!_params) {
                _params = params;
            }
            // 
            isFisrtReload = false;
            DataCenter.SearchModel.getData(_params, function (res) {
                console.log('SearchModel is ready:');
                console.log(res);
                var _value = inputEl ? inputEl.value : '';
                if (_value != '') {
                    self.setState({
                        model: res
                    });
                    self.initIScroll();
                }
            }, function (err) {
                console.log(err);
            });
        },

        gotoBack: function gotoBack() {
            if (inputEl) inputEl.value = '';
            this.setState({
                model: null,
                isHistory: true
            });
            this.props.backCallback(false);
        },

        keySearch: function keySearch(evt) {
            evt.stopPropagation();
            evt.preventDefault();

            var _el = evt.currentTarget;
            inputEl = _el;
            var currKey = 0,
                evt = evt || event;
            currKey = evt.keyCode || evt.which || evt.charCode;
            var _value = _el.value;

            if (_value != '') {
                params.name = _value;
                this.fetchData();
                this.setState({
                    isHistory: false
                });
            } else {
                this.setState({
                    model: null,
                    isHistory: true
                });
            }
            window.event.returnValue = false;
        },
        getLiList: function getLiList(mos, type, language) {
            var _this = this;

            var name = type == 'movie' ? language.movie.movieName : language.cinema.cinemaName;
            var paramsName = params.name;
            var List = mos.map(function (obj, k) {
                if (!obj[name]) return;
                var _names = obj[name].split(paramsName),
                    _len = _names.length;
                var nameList = _names.map(function (item, i) {
                    if (i >= _len - 1) {
                        return React.createElement(
                            'span',
                            null,
                            item
                        );
                    } else {
                        return React.createElement(
                            'span',
                            null,
                            item,
                            React.createElement(
                                'em',
                                null,
                                paramsName
                            )
                        );
                    }
                });

                var _movieName = React.createElement(
                    'span',
                    { className: 'search-nowrap' },
                    nameList
                );

                // var _id = type == "movie" ? "movieName" : obj['cinemaId'];
                return React.createElement(
                    'li',
                    { onClick: _this.handleClick.bind(null, obj['cinemaId'] || obj['movieId'], obj[name], type)
                    },
                    React.createElement(
                        'div',
                        null,
                        _movieName
                    ),
                    React.createElement(
                        'div',
                        null,
                        obj.releaseYear || obj.productTickets
                    )
                );
            });
            return List;
        },
        /**/
        handleClick: function handleClick(id, name, type) {
            // block movieName = ""
            //
            this.setLocalStorageSearchHistory(id, name, type);
            if (inputEl) inputEl.value = '';

            this.setState({
                model: null,
                isHistory: true
            });
            if (name == '' && id == 0) {
                return;
            }
            if (this.props.onItemClick && type == 'movie') {
                this.props.onItemClick(id, name);
            } else if (this.props.onCinemaItemClick && type == 'cinema') {
                this.props.onCinemaItemClick(id, name);
            }
        },

        initIScroll: function initIScroll() {
            this.searchScroll && this.searchScroll.refresh();
            if (!this.searchScroll) {
                var _container = document.getElementsByClassName('search-full')[0];
                _container.style.height = document.body.clientHeight - 44 + 'px';

                this.searchScroll = new IScroll('.search-full', {
                    mouseWheel: true,
                    bindToWrapper: true,
                    preventDefault: Util.iScrollClick(),
                    tap: Util.iScrollClick(),
                    click: Util.iScrollClick()
                });
            }

            // this.movieScroll.on("scroll", this.doSomething);
        },

        setLocalStorageSearchHistory: function setLocalStorageSearchHistory(id, name, type) {
            var concat_arr = localStorage.getItem('data_searchHistory'),
                arr = [];
            if (concat_arr) {
                concat_arr = JSON.parse(concat_arr);
                for (var i = 0; i < concat_arr.length; i++) {
                    if (concat_arr[i].id == id) {
                        concat_arr.splice(i, 1);
                    }
                }
                arr = [{ id: id, name: name, type: type }].concat(concat_arr);
            } else {
                arr = [{ id: id, name: name, type: type }];
            }
            if (arr.length > 8) {
                concat_arr.pop();
            }

            localStorage.setItem('data_searchHistory', JSON.stringify(arr));
        },

        getLocalStorageSearchHistory: function getLocalStorageSearchHistory(id, name, type) {
            var _this2 = this;

            var concat_arr = localStorage.getItem('data_searchHistory'),
                html = '',
                List = '';
            if (concat_arr && concat_arr != '') {

                concat_arr = JSON.parse(concat_arr);
                // this.setState({
                //     searchHistorys: concat_arr
                // })
                List = concat_arr.map(function (obj, k) {
                    return React.createElement(
                        'li',
                        { onClick: _this2.handleClick.bind(null, obj['id'], obj['name'], obj['type'])
                        },
                        React.createElement('i', { className: 'icon-duration' }),
                        obj['name'],
                        ' ',
                        React.createElement('i', { className: 'ico-close' })
                    );
                });

                return React.createElement(
                    'div',
                    { className: 'search-history ' + (this.state.isHistory ? '' : 'cached') },
                    React.createElement(
                        'h4',
                        null,
                        language.info[5]
                    ),
                    React.createElement(
                        'ul',
                        null,
                        List
                    ),
                    React.createElement(
                        'a',
                        { onClick: this.clearSearchHistory.bind() },
                        language.info[4]
                    )
                );
            }
            return '';
        },

        clearSearchHistory: function clearSearchHistory() {
            localStorage.setItem('data_searchHistory', '');
            this.setState({
                searchHistorys: ''
            });
        }

    });

    return Search;
});
