/**
 *  Segmented.jsx
 *   Segmented Control
 *
 *  @props  {Number}    current     
 *  @props  {Function}  onChange    
 *  @return {Component} Segmented
 */

'use strict';

define('segmented',['React'], function (React) {
    'use strict';

    var Segmented = React.createClass({
        displayName: 'Segmented',

        render: function render() {
            var _current = this.props.current;
            var _onChange = this.props.onChange;
            var _menus;
            var th3 = this.props.th3;
            var _item = '';

            if (th3) {
                _item = React.createElement(
                    'li',
                    { className: _current == 2 ? 'current' : '',
                        onClick: _onChange.bind(null, 2) },
                    React.createElement(
                        'a',
                        null,
                        this.props.th3
                    )
                );
            }

            return React.createElement(
                'ul',
                { className: 'seg-wrapper N-s-arc equal-2 fixedUnderNav css-1px' },
                React.createElement(
                    'li',
                    { className: _current == 0 ? 'current' : '',
                        onClick: _onChange.bind(null, 0) },
                    React.createElement(
                        'a',
                        null,
                        this.props.th1
                    )
                ),
                React.createElement(
                    'li',
                    { className: _current == 1 ? 'current' : this.props.th2 ? '' : 'cached',
                        onClick: _onChange.bind(null, 1) },
                    React.createElement(
                        'a',
                        null,
                        this.props.th2
                    )
                ),
                _item
            );
        }
        // shouldComponentUpdate: function(nextProps, nextState) {
        //     if(nextProps.active == "cached"){
        //         return false;
        //     }
        //     return true;
        // }
    });
    return Segmented;
});
