	var ad = function(options){
		// 
		if(!options.container || options.container == '') return;
		this.container = options.container;
		this.resultCallback = options.resultCallback;
		this.callback  = options.callback; 
		// this.DataCenter = options.datacenter;
		// 
		this.init();
		
	};

	ad.prototype.init = function(){
		// advertisingId=19 ID51ID
		this.pageIndex = 0;
		this.fetchData('adlist', function(_adlist){
			this.adListObj = _adlist;
			if(this.adListObj && this.adListObj.advertising && this.adListObj.advertising.advertisements){
				this.adLists = this.adListObj.advertising.advertisements;
				window.adLists = this.adLists;
				this.adListLen = this.adListObj.advertising.advertisements.length;
				if(this.adListLen > 0){
					// if(this.adListLen == 1){

					// }else{
						this.adListImg = [];
						for(var i = 0; i < this.adListLen; i++){
							this.adListImg.push({content: this.adLists[i].img})
						}
					// }
					this.renderDOM();
					this.bindDOM();
				}
			}
		}.bind(this));
			
	};

	ad.prototype.renderDOM = function(){
		if(this.adListImg && this.container){
			this.resultCallback && this.resultCallback();
			this.myiSlider = new iSlider(document.getElementById(this.container), this.adListImg, {
		        isAutoplay: 1,
		        isLooping: 1,
		        isOverspread: 1,
		        animateTime: 500,
		        duration: 5000,
		        plugins: ['dot']
		    });
		}
	};

	ad.prototype.bindDOM = function(){
		if(this.myiSlider){
			this.myiSlider.on('slideChange', function (i, el) {
		    	this.pageIndex = i;
		    }.bind(this))
			var container = document.getElementById(this.container)
		    container.addEventListener('click',function(evt){
		    	// console.log(this.index);
		    	var ad = {
		    		pageIndex: this.pageIndex,
		    		url: this.adLists[this.pageIndex].url,
		    		title: this.adLists[this.pageIndex].title
		    	}
		    	this.callback && this.callback(ad);
		    }.bind(this));
		}
	};

	ad.prototype.fetchData = function(type, callback){
		if(type == 'adlist'){
			// return adlist;
			// return callback && callback(adlist);
			// 51 app     55 web
			if(this.getBannerStatus()){
				var params = 'adlist?advertisingId=51';
			}else{
				var params = 'adlist?advertisingId=55';
			}
			DataCenter.Advertisement.getData(params, function(res){
	            console.log(res)
	            callback && callback(res);
	        },
	        function(err){
	            console.log(err)
	        })
		}
	};

	ad.prototype.getBannerStatus = function(){
        var _afterData = location.href.split('?')[1],
            isApp    = null,
            apps = Util.compareVersion(),
            from = Util.getSearchValue('from'),
            isWepiaoapp = false;
        //wepiao app 
        if(from){
            if(from.toLocaleLowerCase() == 'wepiaoapp'){
                isWepiaoapp = true;
            }
        }
        if(_afterData){
            isApp = Math.round(_afterData.split('#')[0]);
        }
        if(isApp || apps.isApp || isWepiaoapp){
            return true;
        }else{
            
            return false;
        }
    }

	var Advertisement = {
    	ad: ad
    };

    return Advertisement;

    
