'use strict';

define('cinema',['React', 'Chart', 'ReactChart', 'IScroll', 'Router', 'navBar', 'util', 'datacenter', 'commonTable', 'SelectCity', 'SelectDate', 'TimeZone', 'feedbackBlock', 'share', 'pullRefresh'], function (React, Chart, ReactChart, IScroll, Router, NavBar, Util, DataCenter, CommonTable, SelectCity, SelectDate, TimeZone, FeedbackBlock, Share, PullRefresh) {
    'use strict';

    // data
    var timeZoneArr = ['', '', ''];

    //
    var currentDay = Util.prevDay(Util.getTodayStr());

    // 5
    var loseIndex = 1;

    //
    var params = {
        'paging': {
            'page': 1,
            'pageSize': 200
        },
        'movieFilter': {
            'cityId': [],
            'theaterChainId': null,
            'scheduleDate': currentDay + ' 00:00:00',
            'periodType': '0'
        }
    };

    var style = {
        background: 0,
        border: 0,
        fontSize: '1.22rem',
        color: '#D4D2D2'
    };

    // Component
    var Cinema = React.createClass({
        displayName: 'Cinema',

        render: function render() {

            var _scheduleDate = params.movieFilter.scheduleDate.replace(' 00:00:00', ''),
                language = this.props.language.cinema;
            return React.createElement(
                'div',
                { className: 'contentView withFooter withPicker ci-scroller ' + this.props.active
                },
                React.createElement(
                    'ul',
                    { className: 'scroller' },
                    React.createElement(CommonTable, {
                        th1: language.item.th1,
                        th2: language.item.th2,
                        th3: language.item.th3,
                        tr1_field: language.item_tr.tr1_field,
                        tr2_field: 'productTickets',
                        tr3_field: 'productAvgPerson',
                        id_field: 'cinemaId',
                        name_field: language.item_tr.tr1_field,
                        toast: language.toast,
                        index: true,
                        onItemClick: this.props.onCinemaItemClick,
                        movieList: this.state.model && this.state.model.cinemaBoxOffices
                    }),
                    React.createElement(FeedbackBlock, null)
                ),
                React.createElement(NavBar, {
                    bigLogo: true, title: 'Lynn\'s Box',
                    clickSearch: this.props.callbackSearch
                }),
                React.createElement(
                    'div',
                    { className: 'nav_list border-1px' },
                    React.createElement(SelectCity, {
                        klassName: 'contentView withPicker',
                        selectCityCallback: this.selectCityCallback }),
                    React.createElement(SelectDate, {
                        ref: 'SelectDate',
                        date: this.state.date,
                        MAXDate: this.state.date,
                        selectDateCallback: this.selectDateCallback }),
                    React.createElement(
                        'div',
                        { className: 'nav_time', style: style },
                        React.createElement('i', { className: 'icon-duration' }),
                        React.createElement(
                            'span',
                            { className: 'arr-down-' },
                            this.props.language.util.timeZone.titles[0]
                        )
                    )
                )
            );
        },
        //
        getInitialState: function getInitialState() {
            return {
                model: null,
                date: currentDay
            };
        },
        componentDidMount: function componentDidMount() {
            this.fetchData();
            this.initIScroll();
        },
        shouldComponentUpdate: function shouldComponentUpdate(nextProps, nextState) {
            var _active = nextProps.active;
            var _el = document.getElementsByClassName('ci-scroller')[0];
            if (_active != 'active') {
                _el.className = 'contentView withFooter withPicker ci-scroller cached';
                return false;
            } else {
                _el.className = 'contentView withFooter withPicker ci-scroller active';
            }
            return true;
        },
        componentDidUpdate: function componentDidUpdate() {
            if (this.props.active == 'active') {
                this.CIScroll.refresh();
                if (!this.props.cinema_open) {
                    Share.setShare('-');
                }
            }
        },
        fetchData: function fetchData(_params) {
            var self = this;
            if (!_params) {
                _params = params;
            }

            DataCenter.CIModel.getData(_params, function (res) {
                console.log('CIModel is ready:');
                console.log(res);
                if (res && res.cinemaBoxOffices && res.cinemaBoxOffices.length === 0) {
                    self.loseMethod();
                }
                // re-render
                self.setState({
                    model: res
                });
            }, function (err) {
                console.log(err);
            });
        },
        initIScroll: function initIScroll() {
            var self = this;
            //init IScroll
            this.CIScroll = new IScroll('.ci-scroller', {
                mouseWheel: true,
                bindToWrapper: true,
                probeType: 3,
                preventDefault: Util.iScrollClick(),
                tap: Util.iScrollClick(),
                click: Util.iScrollClick()
            });
            var pullRefresh = new PullRefresh.refresh({
                scroll: this.CIScroll,
                model: DataCenter.CIModel,
                params: params,
                callback: function callback(res) {
                    // re-render
                    self.setState({
                        model: res
                    });
                }
            });
            window.app_iScrolls[2] = this.CIScroll;
        },
        //
        selectCityCallback: function selectCityCallback(city) {
            // console.log(city);
            params.movieFilter.cityId = city.cityid;
            if (city.cityid.length == 1 && city.cityid[0] == '0') {
                params.paging.pageSize = 200;
            } else {
                params.paging.pageSize = 20;
            }
            this.fetchData(params);
        },
        //
        selectDateCallback: function selectDateCallback(_date) {
            // console.log(_date);
            //alert(_date);
            params.movieFilter.scheduleDate = _date + ' 00:00:00';
            this.fetchData(params);
        },
        //
        timeZoneCallback: function timeZoneCallback(_value) {
            // console.log(_value);
            //alert(_value);
            params.movieFilter.periodType = _value;
            this.fetchData(params);
        },
        //5
        loseMethod: function loseMethod() {
            loseIndex++;
            if (loseIndex <= 5) {
                var _date = Util.prevDay(currentDay);
                currentDay = _date;
                params.movieFilter.scheduleDate = _date + ' 00:00:00';
                this.fetchData(params);
                this.setState({ date: _date });
                this.refs.SelectDate.setState({ date: _date });
            }
        },

        setPiaoFang: function setPiaoFang() {
            //ga("set", "/cinema", "");
            // ga('send', 'pageview', '/cinema');
            ga('send', 'pageview', {
                'page': '/cinema',
                'title': ''
            });
            //Hawkeye - Wepiao FE Analytics Solution
            he && he('send', 'pageview', 'cinema');
        }
    });

    return Cinema;
});
