/**
 *  Nav Bar.jsx
 *   Navigation Bar
 *
 *  @props  {Bool}      bigLogo          Logo
 *  @props  {Bool}      rightLogo        Logo
 *  @props  {String}    leftNav         
 *  @props  {String}    title           
                            <i
                                className="icon-search"
                                onClick={this.props.clickSearch}>
                            </i>
<i className="icon-setting"></i>
 */

'use strict';

define('navBar',['React'], function (React) {
    'use strict';

    var NavBar = React.createClass({
        displayName: 'NavBar',

        render: function render() {
            var _this = this;

            // let apps = this.state.apps,
            //     setting = '';
            // ios version > 1.1    android version > 1.2
            // if(apps.isApp && ((apps.type = 'iOS' && apps.version > 11) || (apps.type = 'Android' && apps.version > 12)) ){

            //     setting = (
            //         <i
            //             className="icon-setting"
            //             onClick={this.handleSetingClick}>
            //         </i>
            //     )
            // }
            var leftNav = this.props.leftNav && React.createElement(
                'span',
                {
                    onClick: this.props.backCallback,
                    className: 'ico-back' },
                this.props.leftNav
            );

            var rightLogo = this.props.rightLogo && React.createElement(
                'span',
                { className: 'rightLogo' },
                React.createElement('img', { src: 'icon/logo.png' })
            );

            var bigLogo = this.props.bigLogo && React.createElement('img', { className: 'bigLogo', src: 'icon/logo.png' });

            var _title = (function () {
                // just for Lynn's Box version
                if (_this.props.bigLogo) {
                    return React.createElement(
                        'h1',
                        null,
                        React.createElement(
                            'b',
                            null,
                            _this.props.title
                        ),
                        React.createElement(
                            'a',
                            { onClick: _this.handleComingClick, className: 'coming-movie' },
                            _this.props.leftTitle
                        ),
                        React.createElement('i', {
                            className: 'icon-search',
                            onClick: _this.handleClick })
                    );
                }

                if (_this.props.bigLogo) {
                    return React.createElement(
                        'h1',
                        null,
                        React.createElement(
                            'a',
                            {
                                href: '/#/comingmovie',
                                className: 'coming-movie' },
                            _this.props.leftTitle
                        ),
                        bigLogo,
                        React.createElement(
                            'div',
                            { className: 'titleBox' },
                            React.createElement(
                                'b',
                                null,
                                _this.props.title
                            ),
                            React.createElement(
                                'i',
                                null,
                                'piaofang.wepiao.com'
                            )
                        ),
                        React.createElement('i', {
                            className: 'icon-search',
                            onClick: _this.handleClick })
                    );
                } else {
                    return React.createElement(
                        'h1',
                        null,
                        _this.props.title
                    );
                }
            })();

            return React.createElement(
                'header',
                null,
                leftNav,
                _title
            );
        },
        // getInitialState: function(){
        //     return {
        //         apps: Util.compareVersion()
        //     }
        // },
        handleClick: function handleClick() {
            ga('send', 'event', '', 'click', 'search', 1);
            //Hawkeye - Wepiao FE Analytics Solution
            he && he('send', 'event', 'button', 'click', 'search', 1);
            this.props.clickSearch(true);
        },
        handleSetingClick: function handleSetingClick() {
            this.props.clickSeting(true);
        },
        handleComingClick: function handleComingClick() {
            location.hash = '/comingmovie';
        }

    });

    return NavBar;
});
